import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import dashboardService from '../../services/dashboardService';
import { toast } from 'react-toastify';
import Applayout from '../layout/applayout';

const BrokerDashboard = () => {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [dashboardData, setDashboardData] = useState({
        summary: {
            totalActiveLoads: 0,
            assignedJobs: 0,
            acceptedRequests: 0
        },
        activeLoads: {
            total: 0,
            byStatus: {
                active: 0,
                assigned: 0,
                inTransit: 0
            }
        },
        requests: {
            recent: {
                total: 0,
                pending: 0,
                items: []
            },
            statistics: {
                total: 0,
                byStatus: {
                    pending: 0,
                    accepted: 0
                }
            }
        },
        recentPosts: {
            total: 0,
            items: []
        }
    });

    useEffect(() => {
        fetchDashboardData();
    }, []);

    const fetchDashboardData = async () => {
        try {
            setLoading(true);
            const response = await dashboardService.getBrokerDashboard();
            if (response.status === 'success') {
                setDashboardData(response.data);
            }
        } catch (error) {
            toast.error(error.message);
        } finally {
            setLoading(false);
        }
    };

    const formatDate = (dateString) => {
        return new Date(dateString).toLocaleDateString('en-US', {
            year: 'numeric',
            month: 'short',
            day: 'numeric'
        });
    };

    return (

            <div className="container-fluid dashboard-container p-4">
                {/* Stats Row */}
                <div className="row mb-4">
                    <div className="col-sm-6 col-xl-3">
                        <div className="card o-hidden">
                            <div className="bg-primary b-r-4 card-body">
                                <div className="d-flex static-top-widget">
                                    <div className="align-self-center text-center">
                                        <i className="fa fa-truck text-white fa-2x me-3"></i>
                                    </div>
                                    <div className="flex-grow-1">
                                        <span className="m-0 text-white">Active Loads</span>
                                        <h4 className="mb-0 counter text-white">{dashboardData.activeLoads.total}</h4>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col-sm-6 col-xl-3">
                        <div className="card o-hidden">
                            <div className="bg-success b-r-4 card-body">
                                <div className="d-flex static-top-widget">
                                    <div className="align-self-center text-center">
                                        <i className="fa fa-check-circle text-white fa-2x me-3"></i>
                                    </div>
                                    <div className="flex-grow-1">
                                        <span className="m-0 text-white">Assigned Jobs</span>
                                        <h4 className="mb-0 counter text-white">{dashboardData.summary.assignedJobs}</h4>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col-sm-6 col-xl-3">
                        <div className="card o-hidden">
                            <div className="bg-warning b-r-4 card-body">
                                <div className="d-flex static-top-widget">
                                    <div className="align-self-center text-center">
                                        <i className="fa fa-clock text-white fa-2x me-3"></i>
                                    </div>
                                    <div className="flex-grow-1">
                                        <span className="m-0 text-white">Pending Requests</span>
                                        <h4 className="mb-0 counter text-white">{dashboardData.requests.recent.pending}</h4>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col-sm-6 col-xl-3">
                        <div className="card o-hidden">
                            <div className="bg-info b-r-4 card-body">
                                <div className="d-flex static-top-widget">
                                    <div className="align-self-center text-center">
                                        <i className="fa fa-handshake text-white fa-2x me-3"></i>
                                    </div>
                                    <div className="flex-grow-1">
                                        <span className="m-0 text-white">Accepted Requests</span>
                                        <h4 className="mb-0 counter text-white">{dashboardData.summary.acceptedRequests}</h4>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Tables Row */}
                <div className="row">
                    <div className="col-md-6 mb-4">
                        <div className="card h-100">
                            <div className="card-header d-flex justify-content-between align-items-center">
                                <h5 className="mb-0">Recent Requests</h5>
                                {/* <button 
                                    className="btn btn-primary btn-sm"
                                    onClick={() => navigate('/view-requests')}
                                >
                                    View All
                                </button> */}
                            </div>
                            <div className="card-body">
                                <div className="table-responsive">
                                    <table className="table table-hover">
                                        <thead>
                                            <tr>
                                                <th>ID</th>
                                                <th>Carrier</th>
                                                <th>Rate</th>
                                                <th>Status</th>
                                                <th>Date</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {dashboardData.requests.recent.items.map(request => (
                                                <tr key={request.id} className="cursor-pointer" onClick={() => navigate(`/view-requests/${request.loadId}`)}>
                                                    <td>{request.id}</td>
                                                    <td>{request.carrier}</td>
                                                    <td>${request.proposedRate}</td>
                                                    <td>
                                                        <span className={`badge bg-${
                                                            request.status === 'ACCEPTED' ? 'success' : 
                                                            request.status === 'REJECTED' ? 'danger' : 
                                                            'warning'
                                                        }`}>
                                                            {request.status}
                                                        </span>
                                                    </td>
                                                    <td>{formatDate(request.createdAt)}</td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col-md-6 mb-4">
                        <div className="card h-100">
                            <div className="card-header d-flex justify-content-between align-items-center">
                                <h5 className="mb-0">Recent Posts</h5>
                                <button 
                                    className="btn btn-primary btn-sm"
                                    onClick={() => navigate('/view-loads')}
                                >
                                    View All
                                </button>
                            </div>
                            <div className="card-body">
                                <div className="table-responsive">
                                    <table className="table table-hover">
                                        <thead>
                                            <tr>
                                                <th>Type</th>
                                                <th>Route</th>
                                                <th>Pickup</th>
                                                <th>Status</th>
                                                <th>Requests</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {dashboardData.recentPosts.items.map(post => (
                                                <tr key={post.id} className="cursor-pointer">
                                                    <td>{post.loadType}</td>
                                                    <td>
                                                        <small className="d-block">{post.origin} </small>
                                                        <small>{post.destination}</small>
                                                    </td>
                                                    <td>{formatDate(post.pickupDate)}</td>
                                                    <td>
                                                        <span className={`badge bg-${
                                                            post.status === 'ACTIVE' ? 'success' : 
                                                            post.status === 'ASSIGNED' ? 'info' : 
                                                            'warning'
                                                        }`}>
                                                            {post.status}
                                                        </span>
                                                    </td>
                                                    <td>
                                                        <span className="badge bg-primary">{post.pendingRequests}</span>
                                                        <span className="text-muted ms-1">/ {post.requestsCount}</span>
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

    );
};

export default BrokerDashboard; 