import axiosClient from '../axios/axiosClient';

const settingsService = {
    getSettings: async () => {
        try {
            const response = await axiosClient.get('/settings');
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to fetch settings',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    updateLoadBoardUrls: async (loadBoardUrls) => {
        try {
            const response = await axiosClient.patch('/settings/load-board-urls', { loadBoardUrls });
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to update load board URLs',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    updateGeneralSettings: async (settings) => {
        try {
            const response = await axiosClient.patch('/settings/general', settings);
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to update general settings',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    updateAlertPreferences: async (alertPreferences) => {
        try {
            const response = await axiosClient.patch('/settings/alerts', alertPreferences);
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to update alert preferences',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    updateServiceAreas: async (serviceAreas) => {
        try {
            const response = await axiosClient.patch('/settings/service-areas', { serviceAreas });
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to update service areas',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    updateRouteAlerts: async (routeAlerts) => {
        try {
            const response = await axiosClient.patch('/settings/route-alerts', { routeAlerts });
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to update route alerts',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    }
};

export default settingsService; 