// src/routes/Routes.js
import { Routes, Route } from "react-router-dom"
import Page from '../pages/page';
import Login from "../pages/Auth/login";
import Register from "../pages/Auth/register";
import Resetpassword from "../pages/Auth/resetpassword";
import Forgotpassword from "../pages/Auth/forgotpassword";
import NotFound from "../pages/Error/notfound";
import { AuthProvider } from "../context/AuthContext";
import PrivateRoute from "./privateRoute";
import PostLoads from "../pages/PostLoads";
import SearchLoads from "../pages/SearchLoads";
import ViewRequests from "../pages/ViewRequests";
import ViewLoads from "../pages/ViewLoads";
import AllLoads from "../pages/AllLoads";
import Settings from "../pages/Settings";
import Subscription from "../pages/Subscription";
import Profile from "../pages/Auth/UserProfile";
import ManageUsers from "../pages/admin/ManageUsers";
import Analytics from "../pages/admin/Analytics";
import ActivityLogs from "../pages/admin/ActivityLogs";
import LoadboardScraper from "../pages/admin/LoadboardScraper";
import Notifications from '../pages/Notifications';


const Index = () => (
    <AuthProvider>

    <div className="App">
        <Routes>
            <Route path="/login" element={ <Login/> } />
            <Route path="/register" element={ <Register/> } />
            <Route path="/forgot-password" element={ <Forgotpassword/> } />
            <Route path="/reset-password" element={ <Resetpassword/> } />
            <Route 
                    path="/post-loads"  
                    element={<PrivateRoute element={<PostLoads />} />} 
                />
            <Route 
                    path="/search-loads"  
                    element={<PrivateRoute element={<SearchLoads />} />} 
                />
            <Route 
                    path="/view-requests/:loadId"  
                    element={<PrivateRoute element={<ViewRequests />} />} 
                />
            <Route 
                    path="/view-loads"  
                    element={<PrivateRoute element={<ViewLoads />} />} 
                />
            <Route 
                    path="/all-loads"  
                    element={<PrivateRoute element={<AllLoads />} />} 
                />
            <Route 
                    path="/settings"  
                    element={<PrivateRoute element={<Settings />} />} 
                />
            <Route 
                    path="/user-profile" 
                    element={<PrivateRoute element={<Profile />} />} 
                />
            <Route 
                    path="/subscription" 
                    element={<PrivateRoute element={<Subscription />} />} 
                />
            <Route 
                    path="/"  
                    element={<PrivateRoute element={<Page />} />} 
                />
            <Route 
                    path="/admin/users"  
                    element={<PrivateRoute element={<ManageUsers />} />} 
                />
            <Route 
                    path="/admin/analytics"  
                    element={<PrivateRoute element={<Analytics />} />} 
                />
            <Route 
                    path="/admin/activity-logs"  
                    element={<PrivateRoute element={<ActivityLogs />} />} 
                />
                
            <Route 
                    path="/admin/loadboard-scraper"  
                    element={<PrivateRoute element={<LoadboardScraper />} />} 
                />
            <Route 
                    path="/notifications"  
                    element={<PrivateRoute element={<Notifications />} />} 
                />
            <Route path="*" element={<NotFound />} />
        </Routes>
    </div>
    </AuthProvider>
);

export default Index;
