import React, { useEffect, useState } from 'react';
import Applayout from '../layout/applayout';
import axiosClient from '../../axios/axiosClient';
import { format } from 'date-fns';
import { toast } from 'react-toastify';

function Notifications() {
  const [notifications, setNotifications] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filters, setFilters] = useState({
    status: 'all', // 'all', 'read', 'unread'
    type: 'all'    // 'all', 'LOAD_REQUEST', etc.
  });
  const [pagination, setPagination] = useState({
    currentPage: 1,
    totalPages: 1,
    totalItems: 0,
    itemsPerPage: 10
  });

  const fetchNotifications = async (page = 1) => {
    try {
      setLoading(true);
      const statusQuery = filters.status !== 'all' ? `&isRead=${filters.status === 'read'}` : '';
      const typeQuery = filters.type !== 'all' ? `&type=${filters.type}` : '';
      
      const response = await axiosClient.get(
        `/notifications?page=${page}&limit=10${statusQuery}${typeQuery}`
      );
      
      if (response.data.status === 'success') {
        setNotifications(response.data.data.notifications);
        setPagination(response.data.data.pagination);
      }
    } catch (error) {
      toast.error('Failed to fetch notifications');
    } finally {
      setLoading(false);
    }
  };

  const handlePageChange = (page) => {
    fetchNotifications(page);
  };

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: value
    }));
  };

  useEffect(() => {
    fetchNotifications(1);
  }, [filters]); // Refetch when filters change

  const markAsRead = async (id) => {
    try {
      await axiosClient.put(`/notifications/${id}/read`);
      setNotifications(prevNotifications =>
        prevNotifications.map(notif =>
          notif.id === id ? { ...notif, isRead: true } : notif
        )
      );
    } catch (error) {
      toast.error('Failed to mark notification as read');
    }
  };

  const markAllAsRead = async () => {
    try {
      await axiosClient.put('/notifications/mark-all-read');
      setNotifications(prevNotifications =>
        prevNotifications.map(notif => ({ ...notif, isRead: true }))
      );
      toast.success('All notifications marked as read');
    } catch (error) {
      toast.error('Failed to mark all notifications as read');
    }
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col-lg-6">
              <h3>Notifications</h3>
            </div>
            <div className="col-lg-6 text-end">
              <button 
                className="btn btn-primary"
                onClick={markAllAsRead}
              >
                Mark All as Read
              </button>
            </div>
          </div>
        </div>

        {/* Filters Section */}
        <div className="row mb-3">
          <div className="col-md-3">
            <select 
              className="form-select"
              name="status"
              value={filters.status}
              onChange={handleFilterChange}
            >
              <option value="all">All Status</option>
              <option value="read">Read</option>
              <option value="unread">Unread</option>
            </select>
          </div>
          <div className="col-md-3">
            <select 
              className="form-select"
              name="type"
              value={filters.type}
              onChange={handleFilterChange}
            >
              <option value="all">All Types</option>
              <option value="LOAD_REQUEST">Load Request</option>
              {/* Add other notification types as needed */}
            </select>
          </div>
        </div>

        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <div className="card-body">
                {loading ? (
                  <div className="text-center">Loading...</div>
                ) : (
                  <>
                    <div className="table-responsive">
                      <table className="table table-striped">
                        <thead>
                          <tr>
                            <th>Title</th>
                            <th>Message</th>
                            <th>Type</th>
                            <th>Status</th>
                            <th>Date</th>
                            <th>Actions</th>
                          </tr>
                        </thead>
                        <tbody>
                          {notifications.map(notif => (
                            <tr key={notif.id}>
                              <td>{notif.title}</td>
                              <td>{notif.message}</td>
                              <td>{notif.type}</td>
                              <td>
                                <span className={`badge ${notif.isRead ? 'bg-success' : 'bg-warning'}`}>
                                  {notif.isRead ? 'Read' : 'Unread'}
                                </span>
                              </td>
                              <td>{format(new Date(notif.createdAt), 'dd/MM/yyyy')}</td>
                              <td>
                                {!notif.isRead && (
                                  <button 
                                    className="btn btn-primary btn-sm"
                                    onClick={() => markAsRead(notif.id)}
                                  >
                                    Mark as Read
                                  </button>
                                )}
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>

                    {/* Pagination */}
                    <div className="d-flex justify-content-center mt-3">
                      <nav>
                        <ul className="pagination">
                          <li className={`page-item ${!pagination.hasPreviousPage ? 'disabled' : ''}`}>
                            <button
                              className="page-link"
                              onClick={() => handlePageChange(pagination.currentPage - 1)}
                              disabled={!pagination.hasPreviousPage}
                            >
                              Previous
                            </button>
                          </li>
                          {[...Array(pagination.totalPages)].map((_, index) => (
                            <li 
                              key={index + 1}
                              className={`page-item ${pagination.currentPage === index + 1 ? 'active' : ''}`}
                            >
                              <button
                                className="page-link"
                                onClick={() => handlePageChange(index + 1)}
                              >
                                {index + 1}
                              </button>
                            </li>
                          ))}
                          <li className={`page-item ${!pagination.hasNextPage ? 'disabled' : ''}`}>
                            <button
                              className="page-link"
                              onClick={() => handlePageChange(pagination.currentPage + 1)}
                              disabled={!pagination.hasNextPage}
                            >
                              Next
                            </button>
                          </li>
                        </ul>
                      </nav>
                    </div>
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </Applayout>
  );
}

export default Notifications; 