import React, { useState, useEffect } from "react";
import Applayout from "./layout/applayout";
import { useParams } from "react-router-dom";
import loadService from "../services/loadService";
import { toast } from "react-toastify";
import Loader from "../components/app/loader";

function ViewRequests() {
  const { loadId } = useParams();
  const [requests, setRequests] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchRequests();
  }, [loadId]);

  const fetchRequests = async () => {
    try {
      setLoading(true);
      const response = await loadService.getLoadRequests(loadId);
      if (response.status === 'success') {
        console.log(response.data,'response.data')
        setRequests(response.data.requests);
      }
    } catch (error) {
      toast.error(error.message || 'Failed to fetch requests');
    } finally {
      setLoading(false);
    }
  };

  const handleRequestAction = async (requestId, action) => {
    try {
      setLoading(true);
      const status = action === 'accept' ? 'ACCEPTED' : 'REJECTED';
      const responseMessage = action === 'accept' 
        ? "Great! Let's proceed with this load."
        : "Sorry, we cannot proceed with this request.";

      const response = await loadService.updateRequestStatus(
        requestId,
        status,
        responseMessage
      );

      if (response.status === 'success') {
        toast.success(`Request ${action === 'accept' ? 'accepted' : 'rejected'} successfully`);
        await fetchRequests(); // Refresh the requests list
      }
    } catch (error) {
      toast.error(error.message || `Failed to ${action} request`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <div className="page-header-left">
                <h3>View Requests</h3>
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <i className="fa fa-home"></i>
                  </li>
                  <li className="breadcrumb-item">Requests</li>
                  <li className="breadcrumb-item active">View</li>
                </ol>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-header">
                <h4 className="card-title mb-0">Load Requests</h4>
              </div>
              <div className="card-body">
                {loading ? (
                  <Loader />
                ) : (
                  <div className="requests-list">
                    {requests.length === 0 ? (
                      <p className="text-center py-4">No requests found.</p>
                    ) : (
                      requests.map(request => (
                        <div 
                          key={request.id} 
                          className="request-item"
                        >
                          <div className="request-header">
                            <div className="d-flex align-items-center">
                              <span className="request-id me-3">
                                <strong>Request ID:</strong> {request.id}
                              </span>
                              <span className={`status-badge ${request.status.toLowerCase()}`}>
                                {request.status}
                              </span>
                            </div>
                            <div className="request-date">
                              {new Date(request.date).toLocaleDateString()}
                            </div>
                          </div>
                          <div className="request-details">
                            <div className="row">
                              <div className="col-md-6">
                                <p><strong>Job Number:</strong> {request.jobNumber}</p>
                                <p><strong>Requested By:</strong> {request.requestedBy}</p>
                                <p><strong>Proposed Rate:</strong> ${request.proposedRate}</p>
                                <p><strong>Message:</strong> {request.message}</p>
                              </div>
                              <div className="col-md-6">
                                <div className="request-actions text-md-end">
                                  <button 
                                    className="btn btn-success btn-sm me-2"
                                    onClick={() => handleRequestAction(request.id, 'accept')}
                                    disabled={request.status !== 'PENDING'}
                                  >
                                    <i className="fa fa-check me-1"></i>
                                    Accept
                                  </button>
                                  <button 
                                    className="btn btn-danger btn-sm"
                                    onClick={() => handleRequestAction(request.id, 'reject')}
                                    disabled={request.status !== 'PENDING'}
                                  >
                                    <i className="fa fa-times me-1"></i>
                                    Reject
                                  </button>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      ))
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>

      <style jsx>{`
        .requests-list {
          max-width: 100%;
          margin: 0 auto;
        }

        .request-item {
          background: white;
          border: 1px solid #ddd;
          border-radius: 8px;
          padding: 1.5rem;
          margin-bottom: 1rem;
          transition: box-shadow 0.2s ease;
        }

        .request-item:hover {
          box-shadow: 0 2px 8px rgba(0,0,0,0.1);
        }

        .request-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 1rem;
        }

        .status-badge {
          padding: 0.25rem 0.75rem;
          border-radius: 50px;
          font-size: 0.875rem;
          font-weight: 500;
        }

        .status-badge.pending {
          background: #fff3cd;
          color: #856404;
        }

        .status-badge.accepted {
          background: #d4edda;
          color: #155724;
        }

        .status-badge.rejected {
          background: #f8d7da;
          color: #721c24;
        }

        .request-date {
          color: #6c757d;
          font-size: 0.875rem;
        }

        .request-details {
          margin-top: 0.5rem;
        }

        .request-details p {
          margin-bottom: 0.5rem;
        }

        .request-actions {
          margin-top: 1rem;
        }

        @media (min-width: 768px) {
          .request-actions {
            margin-top: 0;
          }
        }

        .btn i {
          font-size: 0.875rem;
        }

        .btn:disabled {
          opacity: 0.6;
          cursor: not-allowed;
        }
      `}</style>
    </Applayout>
  );
}

export default ViewRequests; 