import React, { useState } from 'react';
import { toast } from 'react-toastify';
import getLocationByZip from '../../../utils/zipLookup';

const RFDForm = ({ onSubmit, loading }) => {
    const [formData, setFormData] = useState({
        loadType: 'RFD',
        pickupLocation: '',
        pickupZip: '',
        deliveryLocation: '',
        deliveryZip: '',
        pickupDate: '',
        deliveryDate: '',
        balance: 0,
        cubicFeet: '',
        rate: '',
        equipment: '',
        notes: '',
        mobilePhone: ''
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    };

    const handleZipChange = async (e, type) => {
        const zip = e.target.value;
        if (zip.length === 5) {
            try {
                const locationData = await getLocationByZip(zip);
                setFormData(prev => ({
                    ...prev,
                    [type === 'pickup' ? 'pickupZip' : 'deliveryZip']: zip,
                    [type === 'pickup' ? 'pickupLocation' : 'deliveryLocation']: locationData.location
                }));
            } catch (error) {
                toast.error('Invalid ZIP code');
            }
        }
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        onSubmit(formData);
    };

    return (
        <form onSubmit={handleSubmit}>
            <div className="row g-3">
                <div className="col-md-6">
                    <label className="form-label">Pickup ZIP</label>
                    <input
                        type="text"
                        className="form-control"
                        name="pickupZip"
                        value={formData.pickupZip}
                        onChange={(e) => {
                            handleChange(e);
                            handleZipChange(e, 'pickup');
                        }}
                        required
                        pattern="[0-9]{5}"
                        placeholder="Enter ZIP code"
                    />
                </div>
                <div className="col-md-6">
                    <label className="form-label">Pickup Location</label>
                    <input
                        type="text"
                        className="form-control bg-light text-dark"
                        name="pickupLocation"
                        value={formData.pickupLocation}
                        readOnly
                        placeholder="Will be filled automatically from ZIP"
                        style={{ color: '#495057' }}
                    />
                </div>
                <div className="col-md-6">
                    <label className="form-label">Delivery ZIP</label>
                    <input
                        type="text"
                        className="form-control"
                        name="deliveryZip"
                        value={formData.deliveryZip}
                        onChange={(e) => {
                            handleChange(e);
                            handleZipChange(e, 'delivery');
                        }}
                        required
                        pattern="[0-9]{5}"
                        placeholder="Enter ZIP code"
                    />
                </div>
                <div className="col-md-6">
                    <label className="form-label">Delivery Location</label>
                    <input
                        type="text"
                        className="form-control bg-light text-dark"
                        name="deliveryLocation"
                        value={formData.deliveryLocation}
                        readOnly
                        placeholder="Will be filled automatically from ZIP"
                        style={{ color: '#495057' }}
                    />
                </div>
                <div className="col-md-6">
                    <label className="form-label">Pickup Date</label>
                    <input
                        type="date"
                        className="form-control"
                        name="pickupDate"
                        value={formData.pickupDate}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-6">
                    <label className="form-label">Delivery Date</label>
                    <input
                        type="date"
                        className="form-control"
                        name="deliveryDate"
                        value={formData.deliveryDate}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Balance ($)</label>
                    <input
                        type="number"
                        className="form-control"
                        name="balance"
                        value={formData.balance}
                        onChange={handleChange}
                        required
                        placeholder="Enter balance in dollars"
                        min="0"
                        step="0.01"
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Rate ($)</label>
                    <input
                        type="number"
                        className="form-control"
                        name="rate"
                        value={formData.rate}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Mobile Phone</label>
                    <input
                        type="tel"
                        className="form-control"
                        name="mobilePhone"
                        value={formData.mobilePhone}
                        onChange={handleChange}
                        placeholder="Enter mobile phone (optional)"
                        pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
                    />
                    <small className="text-muted">Format: 123-456-7890</small>
                </div>
                <div className="col-md-4">
                    <label className="form-label">Load Type</label>
                    <select
                        className="form-select"
                        name="loadType"
                        value={formData.loadType}
                        onChange={handleChange}
                        required
                    >
                        <option value="">Select Load Type</option>
                        <option value="full">Full Load</option>
                        <option value="partial">Partial Load</option>
                    </select>
                </div>
                <div className="col-md-4">
                    <label className="form-label">Equipment</label>
                    <select
                        className="form-select"
                        name="equipment"
                        value={formData.equipment}
                        onChange={handleChange}
                        required
                    >
                        <option value="">Select Equipment</option>
                        <option value="flatbed">Flatbed</option>
                        <option value="dryvan">Dry Van</option>
                        <option value="reefer">Reefer</option>
                    </select>
                </div>
                <div className="col-md-4 mb-3">
                    <label className="form-label">Cubic Feet</label>
                    <input
                        type="number"
                        className="form-control"
                        name="cubicFeet"
                        value={formData.cubicFeet}
                        onChange={handleChange}
                        placeholder="Enter cubic feet"
                        min="0"
                        step="0.01"
                    />
                </div>
                <div className="col-12">
                    <label className="form-label">Additional Notes</label>
                    <textarea
                        className="form-control"
                        name="notes"
                        value={formData.notes}
                        onChange={handleChange}
                        rows="3"
                    ></textarea>
                </div>
              
                <div className="col-12">
                    <button type="submit" className="btn btn-primary" disabled={loading}>
                        {loading ? 'Posting...' : 'Post RFD Load'}
                    </button>
                </div>
            </div>
        </form>
    );
};

export default RFDForm; 