import React, { useState } from 'react';
import  getLocationByZip  from '../../../utils/zipLookup';
import { toast } from 'react-toastify';

const RFPForm = ({ onSubmit, loading }) => {
  const [formData, setFormData] = useState({
    loadType: 'RFP',
    pickupLocation: '',
    pickupZip: '',
    deliveryLocation: '',
    deliveryZip: '',
    pickupDate: '',
    deliveryDate: '',
    balance: 0,
    cubicFeet: '',
    rate: 0,
    equipment: '',
    notes: '',
    commodityType: '',
    contactInfo: '',
    mobilePhone: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleZipChange = async (e, type) => {
    const zip = e.target.value;
    if (zip.length === 5) {
      try {
        const locationData = await getLocationByZip(zip);
        setFormData(prev => ({
          ...prev,
          [type === 'pickup' ? 'pickupZip' : 'deliveryZip']: zip,
          [type === 'pickup' ? 'pickupLocation' : 'deliveryLocation']: locationData.location
        }));
      } catch (error) {
        toast.error('Invalid ZIP code');
      }
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(formData);
  };

  return (
    <form onSubmit={handleSubmit}>
      <div className="row">
        <div className="col-md-6">
          <div className="mb-3">
            <label className="form-label">Pickup ZIP</label>
            <input
              type="text"
              className="form-control"
              name="pickupZip"
              value={formData.pickupZip}
              onChange={(e) => {
                handleChange(e);
                handleZipChange(e, 'pickup');
              }}
              required
              pattern="[0-9]{5}"
              placeholder="Enter ZIP code"
            />
          </div>
        </div>
        <div className="col-md-6">
          <div className="mb-3">
            <label className="form-label">Pickup Location</label>
            <input
              type="text"
              className="form-control bg-light text-dark"
              name="pickupLocation"
              value={formData.pickupLocation}
              readOnly
              placeholder="Will be filled automatically from ZIP"
              style={{ color: '#495057' }}
            />
          </div>
        </div>
        <div className="col-md-6">
          <div className="mb-3">
            <label className="form-label">Delivery ZIP</label>
            <input
              type="text"
              className="form-control"
              name="deliveryZip"
              value={formData.deliveryZip}
              onChange={(e) => {
                handleChange(e);
                handleZipChange(e, 'delivery');
              }}
              required
              pattern="[0-9]{5}"
              placeholder="Enter ZIP code"
            />
          </div>
        </div>
        <div className="col-md-6">
          <div className="mb-3">
            <label className="form-label">Delivery Location</label>
            <input
              type="text"
              className="form-control bg-light text-dark"
              name="deliveryLocation"
              value={formData.deliveryLocation}
              readOnly
              placeholder="Will be filled automatically from ZIP"
              style={{ color: '#495057' }}
            />
          </div>
        </div>
        <div className="col-md-4">
          <div className="mb-3">
            <label className="form-label">Pickup Date</label>
            <input
              type="date"
              className="form-control"
              name="pickupDate"
              value={formData.pickupDate}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="col-md-4">
          <div className="mb-3">
            <label className="form-label">Delivery Date</label>
            <input
              type="date"
              className="form-control"
              name="deliveryDate"
              value={formData.deliveryDate}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="col-md-4">
          <div className="mb-3">
            <label className="form-label">Mobile Phone</label>
            <input
              type="tel"
              className="form-control"
              name="mobilePhone"
              value={formData.mobilePhone}
              onChange={handleChange}
              placeholder="Enter mobile phone (optional)"
              pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
            />
            <small className="text-muted">Format: 123-456-7890</small>
          </div>
        </div>
        <div className="col-md-4">
          <div className="mb-3">
            <label className="form-label">Balance ($)</label>
            <input
              type="number"
              className="form-control"
              name="balance"
              value={formData.balance}
              onChange={handleChange}
              required
              placeholder="Enter balance in dollars"
              min="0"
              step="0.01"
            />
          </div>
        </div>
        <div className="col-md-4">
          <div className="mb-3">
            <label className="form-label">Rate ($)</label>
            <input
              type="number"
              className="form-control"
              name="rate"
              value={formData.rate}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        <div className="col-md-4 mb-3">
          <label className="form-label">Cubic Feet</label>
          <input
            type="number"
            className="form-control"
            name="cubicFeet"
            value={formData.cubicFeet}
            onChange={handleChange}
            placeholder="Enter cubic feet"
            min="0"
            step="0.01"
          />
        </div>
       
        <div className="col-12">
          <button 
            type="submit" 
            className="btn btn-primary"
            disabled={loading}
          >
            {loading ? 'Posting...' : 'Post Load'}
          </button>
        </div>
      </div>
    </form>
  );
};

export default RFPForm; 