import React, { useState, useEffect } from "react";
import Applayout from "./layout/applayout";
import loadService from "../services/loadService";
import { toast } from "react-toastify";
import Modal from "../components/app/modal";
import { useNavigate } from "react-router-dom";

function SearchLoads() {
  const [map, setMap] = useState(null);
  const [markers, setMarkers] = useState([]);
  const [jobListings, setJobListings] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [filters, setFilters] = useState({
    loadType: [],
    startDate: '',
    endDate: '',
    equipmentType: '',
    location: '',
  });
  const [selectedJob, setSelectedJob] = useState(null);
  const [modalTitle, setModalTitle] = useState("Load Details");
  const [isMapLoaded, setIsMapLoaded] = useState(false);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const [requestData, setRequestData] = useState({
    proposedRate: '',
    message: "I'm interested in this load. Available with a 53' dry van and can pick up on schedule."
  });

  useEffect(() => {
    // Check if Google Maps API is loaded
    if (window.google && window.google.maps) {
      initMap();
    } else {
      // If not loaded, wait for it
      const checkGoogleMaps = setInterval(() => {
        if (window.google && window.google.maps) {
          clearInterval(checkGoogleMaps);
          initMap();
        }
      }, 100);

      // Cleanup
      return () => clearInterval(checkGoogleMaps);
    }
  }, []);

  const initMap = () => {
    if (!document.getElementById("map")) return;

    try {
      const mapInstance = new window.google.maps.Map(document.getElementById("map"), {
        center: { lat: 39.8283, lng: -98.5795 },
        zoom: 4
      });
      setMap(mapInstance);
      setIsMapLoaded(true);
    } catch (error) {
      console.error('Error initializing map:', error);
      toast.error('Failed to initialize map');
    }
  };

  const updateMap = async () => {
    setIsLoading(true);
    // Clear existing markers and listings
    markers.forEach(marker => marker.setMap(null));
    setMarkers([]);
    setJobListings([]);

    try {
      // Build filter parameters
      const searchParams = {};
      
      // Add selected load types
      const selectedTypes = [];
      if (document.getElementById("show-rfp")?.checked) selectedTypes.push('RFP');
      if (document.getElementById("show-rfd")?.checked) selectedTypes.push('RFD');
      if (document.getElementById("show-open-trucks")?.checked) selectedTypes.push('TRUCK');
      
      if (selectedTypes.length > 0) {
        searchParams.loadType = selectedTypes;
      }

      // Add other filters if they exist
      if (filters.startDate) searchParams.startDate = filters.startDate;
      if (filters.endDate) searchParams.endDate = filters.endDate;
      if (filters.equipmentType) searchParams.equipmentType = filters.equipmentType;
      if (filters.location) searchParams.location = filters.location;

      // Fetch loads from API
      const response = await loadService.searchLoads(searchParams);
      
      if (response.status === 'success') {
        setJobListings(response.data);
        
        // Create markers with labels
        response.data.forEach(load => {
          if (load.type === 'TRUCK') {
            // For trucks, only add one marker at their current location
            createMarker({
              location: load.locationZip,
              label: ` ${load.jobNumber}`,
              type: "Open Truck",
              loadData: load,
              icon: {
                url: 'http://maps.google.com/mapfiles/ms/icons/blue-dot.png',
                labelOrigin: new window.google.maps.Point(11, -10)
              }
            });
          } else {
            // For loads, add origin and destination markers with route
            createMarker({
              location: load.originZip,
              label: ` ${load.jobNumber} (Pickup)`,
              type: `${load.type} Pickup`,
              loadData: load,
              icon: {
                url: 'http://maps.google.com/mapfiles/ms/icons/green-dot.png',
                labelOrigin: new window.google.maps.Point(11, -10)
              }
            });
            
            createMarker({
              location: load.destinationZip,
              label: ` ${load.jobNumber} (Delivery)`,
              type: `${load.type} Delivery`,
              loadData: load,
              icon: {
                url: 'http://maps.google.com/mapfiles/ms/icons/red-dot.png',
                labelOrigin: new window.google.maps.Point(11, -10)
              }
            });

            // Draw route between markers
            const directionsService = new window.google.maps.DirectionsService();
            const directionsRenderer = new window.google.maps.DirectionsRenderer({
              map: map,
              suppressMarkers: true, // Don't show default markers
              polylineOptions: {
                strokeColor: load.type === 'RFP' ? '#4CAF50' : '#f44336',
                strokeOpacity: 0.6,
                strokeWeight: 4
              }
            });

            directionsService.route({
              origin: load.originZip,
              destination: load.destinationZip,
              travelMode: window.google.maps.TravelMode.DRIVING
            }, (response, status) => {
              if (status === 'OK') {
                directionsRenderer.setDirections(response);
                // Store the renderer to clear it later
                setMarkers(prev => [...prev, directionsRenderer]);
              }
            });
          }
        });
      }
    } catch (error) {
      console.error('Error fetching loads:', error);
      toast.error('Failed to fetch loads');
    } finally {
      setIsLoading(false);
    }
  };

  const createMarker = ({ location, label, type, loadData, icon }) => {
    const geocoder = new window.google.maps.Geocoder();

    geocoder.geocode({ address: location }, (results, status) => {
      if (status === "OK") {
        const latLng = results[0].geometry.location;

        // Create marker without label initially
        const marker = new window.google.maps.Marker({
          map: map,
          position: latLng,
          icon: {
            url: icon.url,
            scaledSize: new window.google.maps.Size(30, 30)
          },
          title: `${type}: ${location}`,
          type,
          location,
          jobNumber: loadData.jobNumber
        });

        // Create info window with load details
        const infoWindow = new window.google.maps.InfoWindow({
          content: `
            <div style="padding: 10px;">
              <h6 style="margin: 0 0 5px 0;">${loadData.jobNumber}</h6>
              <p style="margin: 0 0 5px 0;"><strong>${type}</strong></p>
              <p style="margin: 0;">Location: ${location}</p>
              ${loadData.rate ? `<p style="margin: 0;">Rate: $${loadData.rate}</p>` : ''}
            </div>
          `
        });

        // Add hover listeners
        marker.addListener('mouseover', () => {
          infoWindow.open(map, marker);
        });

        marker.addListener('mouseout', () => {
          infoWindow.close();
        });

        // Add click listener for job details
        marker.addListener('click', () => {
          showJobDetails(loadData);
        });

        setMarkers(prev => [...prev, marker]);
      }
    });
  };

  const showJobDetails = (job) => {
    setSelectedJob(job);
    setModalTitle(`Load Details - ${job.jobNumber}`);
  };

  const getCityState = (location) => {
    if (!location) return 'N/A';
    const parts = location.split(',');
    return parts.length > 1 ? `${parts[0]}, ${parts[1].trim()}` : location;
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setRequestData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleRequestLoad = async (jobNumber) => {
    try {
      setLoading(true);
      await loadService.requestLoad(jobNumber, requestData);
      toast.success('Load requested successfully');
      //navigate(`/view-requests/${jobNumber}`);
    } catch (error) {
      toast.error(error.message || 'Failed to request load');
    } finally {
      setLoading(false);
    }
  };

  const clearFilters = () => {
    setFilters({
      loadType: [],
      startDate: '',
      endDate: '',
      equipmentType: '',
      location: '',
    });
    
    // Reset checkboxes
    document.getElementById("show-rfp").checked = false;
    document.getElementById("show-rfd").checked = false;
    document.getElementById("show-open-trucks").checked = false;
    
    // Update map with cleared filters
    updateMap();
  };

  const calculateDistance = (origin, destination) => {
    return new Promise((resolve, reject) => {
      const service = new window.google.maps.DistanceMatrixService();
      
      service.getDistanceMatrix({
        origins: [origin],
        destinations: [destination],
        travelMode: window.google.maps.TravelMode.DRIVING,
        unitSystem: window.google.maps.UnitSystem.IMPERIAL
      }, (response, status) => {
        if (status === 'OK') {
          const distance = response.rows[0].elements[0].distance.text;
          const duration = response.rows[0].elements[0].duration.text;
          resolve({ distance, duration });
        } else {
          reject('Failed to calculate distance');
        }
      });
    });
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <div className="page-header-left">
                <h3>Search Loads</h3>
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <a href="index.html"><i data-feather="home"></i></a>
                  </li>
                  <li className="breadcrumb-item">Loads</li>
                  <li className="breadcrumb-item active">Search</li>
                </ol>
              </div>
            </div>
          </div>
        </div>

        <div id="main-content">
          <div className="search-loads-container">
            <div className="card mb-4">
              <div className="card-header d-flex justify-content-between align-items-center">
                <h5 className="mb-0">Filters</h5>
                <button 
                  className="btn btn-outline-secondary btn-sm"
                  onClick={clearFilters}
                >
                  <i className="fa fa-times me-1"></i>
                  Clear Filters
                </button>
              </div>
              <div className="card-body">
                <div className="filter-controls mb-3">
                  <div className="form-check form-check-inline">
                    <input
                      type="checkbox"
                      className="form-check-input"
                      id="show-rfp"
                      onChange={updateMap}
                    />
                    <label className="form-check-label" htmlFor="show-rfp">
                      Show RFP Jobs
                    </label>
                  </div>
                  <div className="form-check form-check-inline">
                    <input
                      type="checkbox"
                      className="form-check-input"
                      id="show-rfd"
                      onChange={updateMap}
                    />
                    <label className="form-check-label" htmlFor="show-rfd">
                      Show RFD Jobs
                    </label>
                  </div>
                  <div className="form-check form-check-inline">
                    <input
                      type="checkbox"
                      className="form-check-input"
                      id="show-open-trucks"
                      onChange={updateMap}
                    />
                    <label className="form-check-label" htmlFor="show-open-trucks">
                      Show Open Truck Space
                    </label>
                  </div>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-md-8">
                <div className="card">
                  <div className="card-body">
                    <div
                      id="map"
                      style={{
                        height: "500px",
                        width: "100%",
                        position: "relative"
                      }}
                    ></div>
                  </div>
                </div>
              </div>
              <div className="col-md-4">
                <div className="card h-100">
                  <div className="card-header">
                    <h4 className="card-title mb-0">Available Loads</h4>
                  </div>
                  <div className="card-body p-0">
                    <div className="job-list" style={{ 
                      height: 'calc(100vh - 300px)',
                      overflowY: 'auto',
                      overflowX: 'hidden'
                    }}>
                      {console.log(jobListings)}
                      {jobListings.map((job, index) => (
                        <div 
                          key={index}
                          className="job-list-item d-flex align-items-center p-3 border-bottom cursor-pointer"
                          data-bs-toggle="modal"
                          data-bs-target="#jobDetailsModal"
                          onClick={() => showJobDetails(job)}
                          style={{ cursor: 'pointer' }}
                        >
                          <div className="me-3">
                            {job.type === 'TRUCK' && <i className="fa fa-truck text-primary fa-lg"></i>}
                            {job.type === 'RFP' && <i className="fa fa-box text-success fa-lg"></i>}
                            {job.type === 'RFD' && <i className="fa fa-shipping-fast text-warning fa-lg"></i>}
                          </div>
                          <div className="flex-grow-1">
                            <h6 className="mb-1">{job.jobNumber}</h6>
                            <p className="mb-0 text-muted small">
                              <i className="fa fa-map-marker-alt me-1"></i>
                              {getCityState(job.location)}
                            </p>
                            <p className="mb-0 text-muted small">
                              <i className="fa fa-calendar me-1"></i>
                              {new Date(job.availableDate).toLocaleDateString()}
                            </p>
                          </div>
                          <div className="text-end">
                            <span className="badge bg-primary mb-2">${job.ratePerMile}/mile</span>
                            <br/>
                            <span className="text-muted small">{job.equipmentType}</span>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <style jsx>{`
        .job-list-item:hover {
          background-color: #f8f9fa;
        }
        .filter-controls {
          display: flex;
          gap: 1rem;
        }
        .job-list::-webkit-scrollbar {
          width: 6px;
        }

        .job-list::-webkit-scrollbar-track {
          background: #f1f1f1;
        }

        .job-list::-webkit-scrollbar-thumb {
          background: #888;
          border-radius: 3px;
        }

        .job-list::-webkit-scrollbar-thumb:hover {
          background: #555;
        }
      `}</style>

      <Modal
        title={modalTitle}
        modalId="jobDetailsModal"
        modalSize="lg"
        modalPosition="centered"
      >
        {selectedJob && (
          <div className="row">
            <div className="col-12 mb-4">
              <div className="card border-0 shadow-sm">
                <div className="card-body">
                  <div className="row">
                    <div className="col-md-6">
                      <h5 className="text-primary mb-4">Route Information</h5>
                      <div className="mb-3">
                        <label className="text-muted small">Origin</label>
                        <p className="mb-1">{selectedJob.originLocation}</p>
                        <small className="text-muted">ZIP: {selectedJob.originZip}</small>
                      </div>
                      <div className="mb-3">
                        <label className="text-muted small">Destination</label>
                        <p className="mb-1">{selectedJob.destinationLocation}</p>
                        <small className="text-muted">ZIP: {selectedJob.destinationZip}</small>
                      </div>
                    </div>
                    
                    <div className="col-md-6">
                      <h5 className="text-primary mb-4">Load Details</h5>
                      <div className="row">
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Job Number</label>
                          <p className="mb-1">{selectedJob.jobNumber}</p>
                        </div>
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Equipment</label>
                          <p className="mb-1 text-capitalize">{selectedJob.equipmentType}</p>
                        </div>
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Balance</label>
                          <p className="mb-1">${Number(selectedJob.balance).toLocaleString()}</p>
                        </div>
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Rate</label>
                          <p className="mb-1">${Number(selectedJob.rate).toLocaleString()}</p>
                        </div>
                      </div>
                    </div>

                    <div className="col-md-6">
                      <h5 className="text-primary mb-4">Schedule</h5>
                      <div className="row">
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Pickup Date</label>
                          <p className="mb-1">{new Date(selectedJob.pickupDate).toLocaleDateString()}</p>
                        </div>
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Delivery Date</label>
                          <p className="mb-1">{new Date(selectedJob.deliveryDate).toLocaleDateString()}</p>
                        </div>
                      </div>
                    </div>

                    <div className="col-md-6">
                      <h5 className="text-primary mb-4">Contact Information</h5>
                      <div className="mb-3">
                        <label className="text-muted small">Company</label>
                        <p className="mb-1">{selectedJob.contact.company}</p>
                      </div>
                      <div className="row">
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Contact Name</label>
                          <p className="mb-1">{selectedJob.contact.name}</p>
                        </div>
                        <div className="col-6 mb-3">
                          <label className="text-muted small">Phone</label>
                          <p className="mb-1">{selectedJob.contact.phone}</p>
                        </div>
                      </div>
                    </div>

                    <div className="col-12 mt-4">
                      <hr />
                      <h5 className="text-primary mb-4">Submit Request</h5>
                      <div className="row">
                        <div className="col-md-6 mb-3">
                          <label className="form-label">Proposed Rate ($)</label>
                          <input
                            type="number"
                            className="form-control"
                            name="proposedRate"
                            value={requestData.proposedRate}
                            onChange={handleInputChange}
                            placeholder="Enter your proposed rate"
                            required
                          />
                        </div>
                        <div className="col-12 mb-3">
                          <label className="form-label">Message</label>
                          <textarea
                            className="form-control"
                            name="message"
                            value={requestData.message}
                            onChange={handleInputChange}
                            rows="3"
                            placeholder="Enter additional details or requirements"
                            required
                          />
                        </div>
                        <div className="col-12">
                          <button 
                            className="btn btn-primary w-100"
                            onClick={() => handleRequestLoad(selectedJob.id)}
                            disabled={loading}
                          >
                            {loading ? (
                              <span>
                                <i className="fa fa-spinner fa-spin me-2"></i>
                                Submitting Request...
                              </span>
                            ) : (
                              <span>
                                <i className="fa fa-paper-plane me-2"></i>
                                Submit Load Request
                              </span>
                            )}
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </Modal>
    </Applayout>
  );
}

export default SearchLoads; 