import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosClient from '../../../axios/axiosClient';

function SearchLoad() {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [search, setSearch] = useState('');
  const [searchResult, setSearchResult] = useState([]);

  useEffect(() => {
    const delayDebounce = setTimeout(() => {
      if (search.length >= 3) {
        searchLoads();
      } else {
        setSearchResult([]);
      }
    }, 300);

    return () => clearTimeout(delayDebounce);
  }, [search]);

  const searchLoads = async () => {
    setLoading(true);
    try {
      const response = await axiosClient.get(`/loads/search?location=${search}`);
      console.log('Search results:', response.data);
      setSearchResult(response.data.data || []);
    } catch (error) {
      console.error('Search error:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <li className="onhover-dropdown">
      <div className="form-group search-form">
        <input 
          className="form-control"
          type="text" 
          placeholder="Search loads by location..." 
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          style={{ minWidth: '300px' }}
        />
        {loading && (
          <div className="spinner-border spinner-border-sm" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        )}
        
        {searchResult.length > 0 && (
          <div className="dropdown-menu show" style={{
            display: 'block',
            position: 'absolute',
            width: '100%',
            maxHeight: '300px',
            overflowY: 'auto',
            zIndex: 1000,
            marginTop: '2px'
          }}>
            {searchResult.map((load) => (
              <div 
                key={load.id}
                className="dropdown-item"
                onClick={() => navigate(`/view-requests/${load.id}`)}
                style={{ cursor: 'pointer' }}
              >
                <div className="d-flex align-items-center">
                  <i className="fa fa-truck me-2"></i>
                  <div>
                    <div>{load.origin}  {load.destination}</div>
                    <small className="text-muted">
                      Rate: ${load.rate}  Weight: {load.weight}lbs
                    </small>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </li>
  );
}

export default SearchLoad;
