import React, { useState } from 'react';
import  getLocationByZip  from '../../../utils/zipLookup';
import { toast } from 'react-toastify';

const TruckPostForm = ({ onSubmit, loading }) => {
    const [formData, setFormData] = useState({
        loadType: 'TRUCK',
        pickupLocation: '',
        pickupZip: '',
        availableDate: '',
        availableTime: '',
        truckType: '',
        balance: 0,
        dimensions: {
            length: '',
            width: '',
            height: ''
        },
        preferredRoutes: [],
        ratePerMile: '',
        minimumRate: '',
        equipmentDetails: {
            year: '',
            make: '',
            model: ''
        },
        additionalEquipment: [],
        driverInfo: {
            name: '',
            phone: '',
            email: '',
            experience: ''
        },
        certifications: [],
        additionalInfo: '',
        cubicFeet: '',
        mobilePhone: '',
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        if (name.includes('.')) {
            const [parent, child] = name.split('.');
            setFormData(prev => ({
                ...prev,
                [parent]: {
                    ...prev[parent],
                    [child]: value
                }
            }));
        } else {
            setFormData(prev => ({
                ...prev,
                [name]: value
            }));
        }
    };

    const handleZipChange = async (e) => {
        const zip = e.target.value;
        if (zip.length === 5) {
            try {
                const locationData = await getLocationByZip(zip);
                setFormData(prev => ({
                    ...prev,
                    pickupZip: zip,
                    pickupLocation: locationData.location
                }));
            } catch (error) {
                toast.error('Invalid ZIP code');
            }
        }
    };

    const handleSubmit = (e) => {
        console.log(formData, "truck post data")
        e.preventDefault();
        onSubmit(formData);
    };

    return (
        <form onSubmit={handleSubmit}>
            <div className="row g-3">
                {/* Location Information */}
                <div className="col-12">
                    <h5>Current Location</h5>
                </div>
                <div className="col-md-6">
                    <label className="form-label">ZIP Code</label>
                    <input
                        type="text"
                        className="form-control"
                        name="pickupZip"
                        value={formData.pickupZip}
                        onChange={(e) => {
                            handleChange(e);
                            handleZipChange(e);
                        }}
                        required
                        pattern="[0-9]{5}"
                        placeholder="Enter ZIP code"
                    />
                </div>
                <div className="col-md-6">
                    <label className="form-label">Location</label>
                    <input
                        type="text"
                        className="form-control bg-light text-dark"
                        name="pickupLocation"
                        value={formData.pickupLocation}
                        readOnly
                        placeholder="Will be filled automatically from ZIP"
                        style={{ color: '#495057' }}
                    />
                </div>

                {/* Availability */}
                <div className="col-md-6">
                    <label className="form-label">Available Date</label>
                    <input
                        type="date"
                        className="form-control"
                        name="availableDate"
                        value={formData.availableDate}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-6">
                    <label className="form-label">Available Time</label>
                    <input
                        type="time"
                        className="form-control"
                        name="availableTime"
                        value={formData.availableTime}
                        onChange={handleChange}
                    />
                </div>

                {/* Truck Details */}
                <div className="col-12">
                    <h5>Truck Details</h5>
                </div>
                <div className="col-md-6">
                    <label className="form-label">Truck Type</label>
                    <select
                        className="form-select"
                        name="truckType"
                        value={formData.truckType}
                        onChange={handleChange}
                        required
                    >
                        <option value="">Select Truck Type</option>
                        <option value="flatbed">Flatbed</option>
                        <option value="dryvan">Dry Van</option>
                        <option value="reefer">Reefer</option>
                        <option value="stepdeck">Step Deck</option>
                        <option value="lowboy">Lowboy</option>
                        <option value="hotshot">Hotshot</option>
                    </select>
                </div>
                <div className="col-md-6">
                    <label className="form-label">Balance ($)</label>
                    <input
                        type="number"
                        className="form-control"
                        name="balance"
                        value={formData.balance}
                        onChange={handleChange}
                        required
                        placeholder="Enter balance in dollars"
                        min="0"
                        step="0.01"
                    />
                </div>

                {/* Equipment Details */}
                <div className="col-md-4">
                    <label className="form-label">Year</label>
                    <input
                        type="number"
                        className="form-control"
                        name="equipmentDetails.year"
                        value={formData.equipmentDetails.year}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Make</label>
                    <input
                        type="text"
                        className="form-control"
                        name="equipmentDetails.make"
                        value={formData.equipmentDetails.make}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Model</label>
                    <input
                        type="text"
                        className="form-control"
                        name="equipmentDetails.model"
                        value={formData.equipmentDetails.model}
                        onChange={handleChange}
                        required
                    />
                </div>

                {/* Rate Information */}
                <div className="col-md-4">
                    <label className="form-label">Rate per Mile ($)</label>
                    <input
                        type="number"
                        className="form-control"
                        name="ratePerMile"
                        value={formData.ratePerMile}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Minimum Rate ($)</label>
                    <input
                        type="number"
                        className="form-control"
                        name="minimumRate"
                        value={formData.minimumRate}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4 mb-3">
                <label className="form-label">Cubic Feet</label>
                <input
                    type="number"
                    className="form-control"
                    name="cubicFeet"
                    value={formData.cubicFeet}
                    onChange={handleChange}
                    placeholder="Enter cubic feet"
                    min="0"
                    step="0.01"
                />
                </div>

                {/* Driver Information */}
                <div className="col-12">
                    <h5>Driver Information</h5>
                </div>
                <div className="col-md-4">
                    <label className="form-label">Driver Name</label>
                    <input
                        type="text"
                        className="form-control"
                        name="driverInfo.name"
                        value={formData.driverInfo.name}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Years of Experience</label>
                    <input
                        type="number"
                        className="form-control"
                        name="driverInfo.experience"
                        value={formData.driverInfo.experience}
                        onChange={handleChange}
                        required
                    />
                </div>
                <div className="col-md-4">
                    <label className="form-label">Mobile Phone</label>
                    <input
                        type="tel"
                        className="form-control"
                        name="mobilePhone"
                        value={formData.mobilePhone}
                        onChange={handleChange}
                        placeholder="Enter mobile phone (optional)"
                        pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}"
                    />
                    <small className="text-muted">Format: 123-456-7890</small>
                </div>

                {/* Additional Information */}
                <div className="col-12">
                    <label className="form-label">Additional Information</label>
                    <textarea
                        className="form-control"
                        name="additionalInfo"
                        value={formData.additionalInfo}
                        onChange={handleChange}
                        rows="3"
                        placeholder="Enter any additional details about your truck or preferences"
                    ></textarea>
                </div>

                

                <div className="col-12">
                    <button type="submit" className="btn btn-primary">
                        Post Truck
                    </button>
                </div>
            </div>
        </form>
    );
};

export default TruckPostForm; 