import React, { useContext } from "react";
import Applayout from "./layout/applayout";
import AuthContext from "../context/AuthContext";
import Dashboard from "./dashboard/dashboard";
import RFPCarrierDashboard from "./dashboard/RFPCarrierDashboard";
import BrokerDashboard from "./dashboard/BrokerDashboard";
import RFDCarrierDashboard from "./dashboard/RFDCarrierDashboard";
import AdminDashboard from "./admin/AdminDashboard";

function Page() {
  const { user, isAuthenticated } = useContext(AuthContext);

  const renderDashboard = () => {
    if (!user || !isAuthenticated) {
      return <div>Loading...</div>;
    }

    switch (user.userType) {
      case 'BROKER':
        return <BrokerDashboard />;
      case 'RFP_CARRIER':
        return <RFPCarrierDashboard />;
      case 'RFD_CARRIER':
        return <RFDCarrierDashboard />;
      case 'SUPER_ADMIN':
        return <AdminDashboard />;
      default:
        return <div>Invalid user type</div>;
    }
  };

  return (
    <Applayout>
      {renderDashboard()}
    </Applayout>
  );
}

export default Page;
