import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Applayout from "./layout/applayout";
import RFDForm from './PostLoad/forms/RFDForm';
import RFPForm from './PostLoad/forms/RFPForm';
import TruckPostForm from './PostLoad/forms/TruckPostForm';
import loadService from '../services/loadService';
import AuthContext from '../context/AuthContext';
import { toast } from 'react-toastify';

function PostLoad() {
  const navigate = useNavigate();
  const { user } = useContext(AuthContext);
  const [postType, setPostType] = useState('RFP'); // Default for BROKER
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (user) {
      switch (user.userType) {
        case 'BROKER':
          setPostType('RFP');
          break;
        case 'RFP_CARRIER':
          setPostType('RFD');
          break;
        case 'RFD_CARRIER':
          setPostType('TRUCK');
          break;
        default:
          setPostType('RFP');
      }
    }
  }, [user]);

  // Transform form data to match API requirements
  const transformFormData = (formData, type) => {
    switch (type) {
      case 'RFP':
        return {
          pickupLocation: formData.pickupLocation,
          pickupZip: formData.pickupZip,
          deliveryLocation: formData.deliveryLocation,
          deliveryZip: formData.deliveryZip,
          pickupDate: formData.pickupDate,
          deliveryDate: formData.deliveryDate,
          balance: parseFloat(formData.balance) || 0,
          cubicFeet: parseFloat(formData.cubicFeet) || 0,
          rate: parseFloat(formData.rate) || 0,
          equipment: formData.requiredEquipment,
          notes: formData.specialInstructions,
          loadType: type,
          commodityType: formData.commodityType,
          contactInfo: formData.contactInfo
        };

      case 'RFD':
        return {
          pickupLocation: formData.pickupLocation,
          pickupZip: formData.pickupZip,
          deliveryLocation: formData.deliveryLocation,
          deliveryZip: formData.deliveryZip,
          pickupDate: formData.pickupDate,
          deliveryDate: formData.deliveryDate,
          balance: parseFloat(formData.balance) || 0,
          cubicFeet: parseFloat(formData.cubicFeet) || 0,
          rate: parseFloat(formData.rate) || 0,
          equipment: formData.equipment,
          notes: formData.notes,
          loadType: type,
        };

      case 'TRUCK':
        return {
          ...formData,
          pickupLocation: formData.pickupLocation,
          cubicFeet: parseFloat(formData.cubicFeet) || 0,
          pickupZip: formData.pickupZip,
          pickupDate: formData.availableDate,
          truckType: formData.truckType,
          balance: formData.capacity,
          rate: parseFloat(formData.ratePerMile) || 0,
          additionalInfo: formData.additionalInfo,
          loadType: type,
        };

      default:
        return formData;
    }
  };

  const handleSubmit = async (formData) => {
    setError('');
    setLoading(true);

    try {
      const loadData = {
        loadType: postType,
        ...transformFormData(formData, postType)
      };
      await loadService.createLoad(loadData);
      toast.success('Post created successfully!');
      navigate('/view-loads');
    } catch (err) {
      toast.error(err.message || 'Failed to create post');
      setError(err.message || 'Failed to create post');
    } finally {
      setLoading(false);
    }
  };

  if (!user) {
    return <div>Loading...</div>;
  }

  

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <div className="page-header-left">
                <h3>Post Load</h3>
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <a href="/dashboard"><i data-feather="home"></i></a>
                  </li>
                  <li className="breadcrumb-item active">Post Load</li>
                </ol>
              </div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="card-body">
            {error && (
              <div className="alert alert-danger" role="alert">
                {error}
              </div>
            )}

            {user.userType === 'BROKER' && (
              <RFPForm onSubmit={handleSubmit} loading={loading} />
            )}

            {user.userType === 'RFP_CARRIER' && (
              <RFDForm onSubmit={handleSubmit} loading={loading} />
            )}

            {user.userType === 'RFD_CARRIER' && (
              <TruckPostForm onSubmit={handleSubmit} loading={loading} />
            )}
          </div>
        </div>
      </div>
    </Applayout>
  );
}

export default PostLoad;