import React from 'react';

const SubscriptionPlans = ({ currentPlan, onSelectPlan }) => {
  const plans = [
    {
      id: 'basic',
      name: 'Basic',
      price: 29.99,
      interval: 'month',
      features: [
        'Basic Load Board Access',
        'Email Alerts',
        'Up to 3 Service Areas',
        'Standard Support'
      ]
    },
    {
      id: 'pro',
      name: 'Professional',
      price: 49.99,
      interval: 'month',
      features: [
        'Advanced Load Board Access',
        'Priority Alerts',
        'Unlimited Service Areas',
        'Route Planning',
        'Priority Support'
      ],
      recommended: true
    },
    {
      id: 'enterprise',
      name: 'Enterprise',
      price: 99.99,
      interval: 'month',
      features: [
        'Full Load Board Access',
        'Real-time Alerts',
        'Unlimited Everything',
        'API Access',
        'Dedicated Support',
        'Custom Integration'
      ]
    }
  ];

  return (
    <div className="subscription-plans">
      <div className="row">
        {plans.map((plan) => (
          <div key={plan.id} className="col-md-4 mb-4">
            <div className={`card h-100 ${plan.recommended ? 'border-primary' : ''}`}>
              {plan.recommended && (
                <div className="card-header bg-primary text-white text-center">
                  Recommended
                </div>
              )}
              <div className="card-body d-flex flex-column">
                <h5 className="card-title">{plan.name}</h5>
                <div className="price mb-3">
                  <span className="h2">${plan.price}</span>
                  <span className="text-muted">/{plan.interval}</span>
                </div>
                <ul className="list-unstyled mb-4">
                  {plan.features.map((feature, index) => (
                    <li key={index} className="mb-2">
                      <i className="fa fa-check text-success me-2"></i>
                      {feature}
                    </li>
                  ))}
                </ul>
                <button
                  className={`btn ${
                    currentPlan === plan.id 
                      ? 'btn-success' 
                      : plan.recommended 
                        ? 'btn-primary' 
                        : 'btn-outline-primary'
                  } mt-auto`}
                  onClick={() => onSelectPlan(plan)}
                  disabled={currentPlan === plan.id}
                >
                  {currentPlan === plan.id ? 'Current Plan' : 'Select Plan'}
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      <style jsx>{`
        .subscription-plans .card {
          transition: transform 0.2s;
          border-radius: 10px;
        }
        
        .subscription-plans .card:hover {
          transform: translateY(-5px);
          box-shadow: 0 4px 15px rgba(0,0,0,0.1);
        }

        .price {
          color: #333;
        }

        .card-header {
          border-radius: 10px 10px 0 0;
        }
      `}</style>
    </div>
  );
};

export default SubscriptionPlans; 