import React, { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import axiosClient from '../../axios/axiosClient';
import Applayout from '../layout/applayout';

function Analytics() {
  const [loading, setLoading] = useState(true);
  const [analyticsData, setAnalyticsData] = useState({
    userStats: {
      totalUsers: 0,
      activeUsers: 0,
      newUsersThisMonth: 0
    },
    loadStats: {
      totalLoads: 0,
      activeLoads: 0,
      completedLoads: 0,
      averageLoadValue: 0
    },
    revenueStats: {
      totalRevenue: 0,
      monthlyRevenue: 0,
      averageLoadRevenue: 0
    }
  });

  useEffect(() => {
    fetchAnalytics();
  }, []);

  const fetchAnalytics = async () => {
    try {
      const response = await axiosClient.get('/admin/analytics');
      if (response.data.status === 'success') {
        setAnalyticsData(response.data.data);
      }
    } catch (error) {
      console.error('Analytics fetch error:', error);
      toast.error('Failed to fetch analytics data');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <Applayout>
        <div className="loader-wrapper">
          <div className="loader-index"><span></span></div>
        </div>
      </Applayout>
    );
  }

  const { userStats, loadStats, revenueStats } = analyticsData;

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <h3>System Analytics</h3>
            </div>
          </div>
        </div>

        <div className="row">
          {/* User Statistics */}
          <div className="col-md-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title">User Statistics</h5>
                <div className="row g-3 mt-2">
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Total Users</span>
                      <h3>{userStats?.totalUsers || 0}</h3>
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Active Users</span>
                      <h3>{userStats?.activeUsers || 0}</h3>
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>New Users (This Month)</span>
                      <h3>{userStats?.newUsersThisMonth || 0}</h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Load Statistics */}
          <div className="col-md-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title">Load Statistics</h5>
                <div className="row g-3 mt-2">
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Total Loads</span>
                      <h3>{loadStats?.totalLoads || 0}</h3>
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Active Loads</span>
                      <h3>{loadStats?.activeLoads || 0}</h3>
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Completed Loads</span>
                      <h3>{loadStats?.completedLoads || 0}</h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Revenue Statistics */}
          <div className="col-md-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title">Revenue Statistics</h5>
                <div className="row g-3 mt-2">
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Total Revenue</span>
                      <h3>${(revenueStats?.totalRevenue || 0).toLocaleString()}</h3>
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Monthly Revenue</span>
                      <h3>${(revenueStats?.monthlyRevenue || 0).toLocaleString()}</h3>
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex justify-content-between align-items-center">
                      <span>Average Load Revenue</span>
                      <h3>${(revenueStats?.averageLoadRevenue || 0).toLocaleString()}</h3>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Applayout>
  );
}

export default Analytics; 