import React, { useContext, useState } from 'react'
import axiosClient from '../../axios/axiosClient';
import AuthContext from '../../context/AuthContext';
import logo from "../../assets/images/fdo.png"
import Loader from '../../components/app/loader';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

function Login() {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loading, setLoading] = useState(false);
    const { login } = useContext(AuthContext);
    const navigate = useNavigate();

    const handleLogin = async (e) => {
        e.preventDefault();
        setLoading(true);
        
        try {
            const response = await axiosClient.post('/auth/login', { email, password });
            const { data } = response;
            
            if (data.status === 'success') {
                const { token, user } = data.data;
                login(token, user);
                toast.success('Login Successful');
                navigate('/');
            }
        } catch (error) {
            console.error('Login error:', error);
            toast.error(error.response?.data?.message || 'Login failed');
        } finally {
            setLoading(false);
        }
    };

    const signUp = () => {
        navigate('/register');
    }

    return (
        <>

         <div className="container-fluid p-0">
        <div className="authentication-main">
          <div className="row">
            <div className="col-md-12">
              <div className="auth-innerright">
                <div className="authentication-box">
                  <div className="text-center">
                    {logo ? (
                        <img src={logo} alt="Company Logo" style={{height:"100px"}} />
                    ) : (
                        <h2>Company Name</h2>
                    )}
                  </div>
                  <div className="card mt-4">
                    <div className="card-body">
                    <>
                        <div className="text-center">
                            <h4>LOGIN</h4>
                            <h6>Enter your Username and Password </h6>
                        </div>
                        <form className="theme-form" onSubmit={handleLogin}>
                            <div className="mb-3">
                            <label className="col-form-label pt-0">Email</label>
                            <input className="form-control" type="email" required="" value={email}
                        onChange={(e) => setEmail(e.target.value)} />
                            </div>
                            <div className="mb-3">
                            <label className="col-form-label">Password</label>
                            <input className="form-control" type="password" required=""   value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        />
                            </div>
                            <div className="checkbox p-0">
                            <input id="checkbox1" type="checkbox" />
                            <label for="checkbox1">Remember me</label>
                            </div>
                            <div className="form-row mt-3">
                            <button className="btn btn-primary btn-block w-100" type="submit" disabled={loading}>Login</button>
                            </div>
                            <div className="form-row mt-3">
                            <button className="btn btn-secondary btn-block w-100" type="button" onClick={signUp}>Sign Up</button>
                            </div>
                            {/* <div className="login-divider"></div> */}
                            {/* <div className="social mt-3">
                            <div className="form-group btn-showcase d-flex">
                                <button className="btn social-btn btn-fb d-inline-block"> <i className="fa fa-facebook"></i></button>
                                <button className="btn social-btn btn-twitter d-inline-block"><i className="fa fa-google"></i></button>
                                <button className="btn social-btn btn-google d-inline-block"><i className="fa fa-twitter"></i></button>
                                <button className="btn social-btn btn-github d-inline-block"><i className="fa fa-github"></i></button>
                            </div>
                            </div> */}
                        </form>
                        {loading && <Loader />}
                        </>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
        </>
    )
}

export default Login
