import React, { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import axiosClient from '../../axios/axiosClient';
import Applayout from '../layout/applayout';

function AdminDashboard() {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [dashboardData, setDashboardData] = useState({
    stats: {
      activeLoads: 0,
      pendingRequests: 0,
      completedLoads: 0,
      revenue: 0
    },
    recentLoads: [],
    recentRequests: []
  });

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      const response = await axiosClient.get('/admin');
      if (response.data.status === 'success') {
        setDashboardData(response.data.data);
      }
    } catch (error) {
      toast.error('Failed to fetch dashboard data');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <div className="loader-wrapper">
      <div className="loader-index"><span></span></div>
    </div>;
  }

  return (

      <div className="container-fluid dashboard-container p-4">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <h3>Admin Dashboard</h3>
            </div>
          </div>
        </div>

        {/* Stats Row */}
        <div className="row mb-4">
          <div className="col-sm-6 col-xl-3">
            <div className="card o-hidden">
              <div className="bg-primary b-r-4 card-body">
                <div className="d-flex static-top-widget">
                  <div className="align-self-center text-center">
                    <i className="fa fa-truck text-white fa-2x me-3"></i>
                  </div>
                  <div className="flex-grow-1">
                    <span className="m-0 text-white">Active Loads</span>
                    <h4 className="mb-0 counter text-white">{dashboardData.stats.activeLoads}</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-sm-6 col-xl-3">
            <div className="card o-hidden">
              <div className="bg-secondary b-r-4 card-body">
                <div className="d-flex static-top-widget">
                  <div className="align-self-center text-center">
                    <i className="fa fa-clock text-white fa-2x me-3"></i>
                  </div>
                  <div className="flex-grow-1">
                    <span className="m-0 text-white">Pending Requests</span>
                    <h4 className="mb-0 counter text-white">{dashboardData.stats.pendingRequests}</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-sm-6 col-xl-3">
            <div className="card o-hidden">
              <div className="bg-success b-r-4 card-body">
                <div className="d-flex static-top-widget">
                  <div className="align-self-center text-center">
                    <i className="fa fa-check-circle text-white fa-2x me-3"></i>
                  </div>
                  <div className="flex-grow-1">
                    <span className="m-0 text-white">Completed Loads</span>
                    <h4 className="mb-0 counter text-white">{dashboardData.stats.completedLoads}</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-sm-6 col-xl-3">
            <div className="card o-hidden">
              <div className="bg-info b-r-4 card-body">
                <div className="d-flex static-top-widget">
                  <div className="align-self-center text-center">
                    <i className="fa fa-dollar-sign text-white fa-2x me-3"></i>
                  </div>
                  <div className="flex-grow-1">
                    <span className="m-0 text-white">Revenue</span>
                    <h4 className="mb-0 counter text-white">${dashboardData.stats.revenue.toLocaleString()}</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Recent Activities */}
        <div className="row">
          <div className="col-md-6 mb-4">
            <div className="card h-100">
              <div className="card-header d-flex justify-content-between align-items-center">
                <h5 className="mb-0">Recent Loads</h5>
                <button 
                  className="btn btn-primary btn-sm"
                  onClick={() => navigate('/admin/loads')}
                >
                  View All
                </button>
              </div>
              <div className="card-body">
                <div className="table-responsive">
                  <table className="table table-hover">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>Route</th>
                        <th>Status</th>
                        <th>Date</th>
                      </tr>
                    </thead>
                    <tbody>
                      {dashboardData.recentLoads.map(load => (
                        <tr key={load.id} className="cursor-pointer">
                          <td>{load.id}</td>
                          <td>{load.origin}  {load.destination}</td>
                          <td>
                            <span className={`badge bg-${load.status === 'ACTIVE' ? 'success' : 'warning'}`}>
                              {load.status}
                            </span>
                          </td>
                          <td>{new Date(load.createdAt).toLocaleDateString()}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-6 mb-4">
            <div className="card h-100">
              <div className="card-header d-flex justify-content-between align-items-center">
                <h5 className="mb-0">Recent Requests</h5>
                <button 
                  className="btn btn-primary btn-sm"
                  onClick={() => navigate('/admin/requests')}
                >
                  View All
                </button>
              </div>
              <div className="card-body">
                <div className="table-responsive">
                  <table className="table table-hover">
                    <thead>
                      <tr>
                        <th>ID</th>
                        <th>Company</th>
                        <th>Status</th>
                        <th>Date</th>
                      </tr>
                    </thead>
                    <tbody>
                      {dashboardData.recentRequests.map(request => (
                        <tr key={request.id} className="cursor-pointer">
                          <td>{request.id}</td>
                          <td>{request.company}</td>
                          <td>
                            <span className={`badge bg-${request.status === 'PENDING' ? 'warning' : 'success'}`}>
                              {request.status}
                            </span>
                          </td>
                          <td>{new Date(request.date).toLocaleDateString()}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

  );
}

export default AdminDashboard; 