import React, { useState } from 'react';
import { toast } from 'react-toastify';
import settingsService from '../services/settingsService';

function LoadBoardLinks({ loadBoardUrls = [], onChange }) {
    const [newUrl, setNewUrl] = useState('');
    const [loading, setLoading] = useState(false);

    const handleAddUrl = async () => {
        if (newUrl.trim()) {
            try {
                setLoading(true);
                const updatedUrls = [...loadBoardUrls, newUrl.trim()];
                await settingsService.updateLoadBoardUrls(updatedUrls);
                onChange(updatedUrls);
                toast.success('Load board URL added successfully');
            } catch (error) {
                toast.error(error.message);
            } finally {
                setLoading(false);
            }
            setNewUrl('');
        }
    };

    const handleRemoveUrl = async (indexToRemove) => {
        try {
            setLoading(true);
            const updatedUrls = loadBoardUrls.filter((_, index) => index !== indexToRemove);
            await settingsService.updateLoadBoardUrls(updatedUrls);
            onChange(updatedUrls);
            toast.success('Load board URL removed successfully');
        } catch (error) {
            toast.error(error.message);
        } finally {
            setLoading(false);
        }
    };

    const handleEditUrl = (index, updatedUrl) => {
        const newUrls = [...loadBoardUrls];
        newUrls[index] = updatedUrl;
        onChange(newUrls);
    };

    return (
        <div className="load-board-links">
            <div className="mb-3">
                <label className="form-label">Add Your Favorite Load Boards For Real Time Alerts</label>
                <div className="input-group">
                    <input
                        type="url"
                        className="form-control"
                        placeholder="Enter load board URL"
                        value={newUrl}
                        onChange={(e) => setNewUrl(e.target.value)}
                    />
                    <button 
                        className="btn btn-primary" 
                        type="button"
                        onClick={handleAddUrl}
                    >
                        Add
                    </button>
                </div>
            </div>

            {loadBoardUrls.length > 0 && (
                <div className="list-group mt-3">
                    {loadBoardUrls.map((url, index) => (
                        <div key={index} className="list-group-item list-group-item-action d-flex justify-content-between align-items-center">
                            <input
                                type="url"
                                className="form-control form-control-sm me-2"
                                value={url}
                                onChange={(e) => handleEditUrl(index, e.target.value)}
                            />
                            <button
                                className="btn btn-danger btn-sm"
                                onClick={() => handleRemoveUrl(index)}
                            >
                                <i className="fa fa-trash"></i>
                            </button>
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
}

export default LoadBoardLinks; 