import React, { useState, useEffect } from "react";
import Applayout from "./layout/applayout";
import LoadBoardLinks from "../components/LoadBoardLinks";
import settingsService from '../services/settingsService';
import { toast } from 'react-toastify';
import axiosClient from '../axios/axiosClient';
import SyncLoadboardButton from '../components/SyncLoadboardButton';

function Settings() {
  const [formData, setFormData] = useState({
    notifications: true,
    emailAlerts: true,
    language: 'en',
    timezone: 'UTC',
    loadBoardUrls: [],
    serviceAreas: [], // Array of {zipCode, radius}
    alertPreferences: {
      rfpAlerts: true,
      openTruckAlerts: false
    },
    routeAlerts: [], // Array of {originZip, destinationZip}
    currentLocation: null
  });

  const [loading, setLoading] = useState(false);

  // Location handling
  const getCurrentLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setFormData(prev => ({
            ...prev,
            currentLocation: {
              lat: position.coords.latitude,
              lng: position.coords.longitude
            }
          }));
        },
        (error) => {
          console.error("Error getting location:", error);
          alert("Unable to get your location. Please check your settings.");
        }
      );
    }
  };

  // Service Area handlers
  const addServiceArea = () => {
    setFormData(prev => ({
      ...prev,
      serviceAreas: [...prev.serviceAreas, { zipCode: '', radius: 50 }]
    }));
  };

  const updateServiceArea = (index, field, value) => {
    setFormData(prev => ({
      ...prev,
      serviceAreas: prev.serviceAreas.map((area, i) => 
        i === index ? { ...area, [field]: value } : area
      )
    }));
  };

  const removeServiceArea = (index) => {
    setFormData(prev => ({
      ...prev,
      serviceAreas: prev.serviceAreas.filter((_, i) => i !== index)
    }));
  };

  // Route Alert handlers
  const addRouteAlert = () => {
    setFormData(prev => ({
      ...prev,
      routeAlerts: [...prev.routeAlerts, { originZip: '', destinationZip: '' }]
    }));
  };

  const updateRouteAlert = (index, field, value) => {
    setFormData(prev => ({
      ...prev,
      routeAlerts: prev.routeAlerts.map((route, i) => 
        i === index ? { ...route, [field]: value } : route
      )
    }));
  };

  const removeRouteAlert = (index) => {
    setFormData(prev => ({
      ...prev,
      routeAlerts: prev.routeAlerts.filter((_, i) => i !== index)
    }));
  };

  // Add these handlers
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      const { loadBoardUrls, ...generalSettings } = formData;
      
      // Update general settings
      await settingsService.updateGeneralSettings(generalSettings);
      
      // Update load board URLs
      await settingsService.updateLoadBoardUrls(loadBoardUrls);
      
      toast.success('Settings updated successfully');
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    const { name, type, checked, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  useEffect(() => {
    const fetchSettings = async () => {
      try {
        setLoading(true);
        const response = await settingsService.getSettings();
        if (response && response.data) {
          setFormData(prev => ({
            ...prev,
            ...response.data,
            // Ensure arrays are initialized even if API doesn't return them
            serviceAreas: response.data.serviceAreas || [],
            routeAlerts: response.data.routeAlerts || [],
            loadBoardUrls: response.data.loadBoardUrls || []
          }));
        }
      } catch (error) {
        toast.error(error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchSettings();
  }, []);

  const handleSaveAlertPreferences = async () => {
    try {
      setLoading(true);
      await settingsService.updateAlertPreferences(formData.alertPreferences);
      toast.success('Alert preferences updated successfully');
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleSaveServiceAreas = async () => {
    try {
      setLoading(true);
      await settingsService.updateServiceAreas(formData.serviceAreas);
      toast.success('Service areas updated successfully');
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  const handleSaveRouteAlerts = async () => {
    try {
      setLoading(true);
      await settingsService.updateRouteAlerts(formData.routeAlerts);
      toast.success('Route alerts updated successfully');
    } catch (error) {
      toast.error(error.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <div className="page-header-left">
                <h3>Settings</h3>
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <a href="index.html">
                      <i data-feather="home"></i>
                    </a>
                  </li>
                  <li className="breadcrumb-item active">Settings</li>
                </ol>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-md-6 mb-4">
            <div className="card">
              <div className="card-header">
                <h4 className="card-title mb-0">General Settings</h4>
              </div>
              <div className="card-body">
                <form onSubmit={handleSubmit}>
                  <div className="row">
                    <div className="col-md-12">
                      <div className="form-group mb-3">
                        <label className="form-label">Language</label>
                        <select 
                          className="form-select"
                          name="language"
                          value={formData.language}
                          onChange={handleChange}
                        >
                          <option value="en">English</option>
                          <option value="es">Spanish</option>
                          <option value="fr">French</option>
                        </select>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="form-group mb-3">
                        <label className="form-label">Timezone</label>
                        <select 
                          className="form-select"
                          name="timezone"
                          value={formData.timezone}
                          onChange={handleChange}
                        >
                          <option value="UTC">UTC</option>
                          <option value="EST">EST</option>
                          <option value="PST">PST</option>
                        </select>
                      </div>
                    </div>
                  </div>

                  <div className="form-check mb-3">
                    <input 
                      type="checkbox" 
                      className="form-check-input" 
                      id="notifications"
                      name="notifications"
                      checked={formData.notifications}
                      onChange={handleChange}
                    />
                    <label className="form-check-label" htmlFor="notifications">
                      Notifications
                    </label>
                  </div>
                  <div className="form-check mb-3">
                    <input 
                      type="checkbox" 
                      className="form-check-input" 
                      id="emailAlerts"
                      name="emailAlerts"
                      checked={formData.emailAlerts}
                      onChange={handleChange}
                    />
                    <label className="form-check-label" htmlFor="emailAlerts">
                      Email Alerts
                    </label>
                  </div>

                  <button type="submit" className="btn btn-primary">
                    Save Changes
                  </button>
                </form>
              </div>
            </div>
          </div>

          <div className="col-md-6 mb-4">
            <div className="card">
              <div className="card-header d-flex justify-content-between align-items-center">
                <h5 className="mb-0">Loadboard URLs</h5>
                {formData.loadBoardUrls?.length > 0 && <SyncLoadboardButton />}
              </div>
              <div className="card-body">
                <LoadBoardLinks
                  loadBoardUrls={formData.loadBoardUrls}
                  onChange={(urls) => setFormData(prev => ({ ...prev, loadBoardUrls: urls }))}
                />
                
                {/* Display submitted load boards */}
                {formData.loadBoardUrls.length > 0 && (
                  <div className="mt-4">
                    <h5>Your Load Boards</h5>
                    <div className="list-group">
                      {/* {formData.loadBoardUrls.map((url, index) => (
                        <div key={index} className="list-group-item">
                          <a href={url} target="_blank" rel="noopener noreferrer">
                            {url}
                          </a>
                        </div>
                      ))} */}
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>

          {/* Service Areas Card */}
          <div className="col-md-6 mb-4">
            <div className="card h-100">
              <div className="card-header d-flex justify-content-between align-items-center">
                <h5 className="mb-0">Service Areas</h5>
                <button 
                  className="btn btn-primary btn-sm"
                  onClick={() => {
                    setFormData(prev => ({
                      ...prev,
                      serviceAreas: [...prev.serviceAreas || [], { zipCode: '', radius: 50 }]
                    }));
                  }}
                >
                  Add Service Area
                </button>
              </div>
              <div className="card-body">
                <div className="table-responsive">
                  <table className="table table-hover">
                    <thead>
                      <tr>
                        <th>Zip Code</th>
                        <th>Radius (miles)</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {(formData.serviceAreas || []).map((area, index) => (
                        <tr key={index}>
                          <td>
                            <input
                              type="text"
                              className="form-control"
                              value={area.zipCode}
                              onChange={(e) => {
                                const newAreas = [...formData.serviceAreas];
                                newAreas[index].zipCode = e.target.value;
                                setFormData(prev => ({ ...prev, serviceAreas: newAreas }));
                              }}
                            />
                          </td>
                          <td>
                            <input
                              type="number"
                              className="form-control"
                              value={area.radius}
                              onChange={(e) => {
                                const newAreas = [...formData.serviceAreas];
                                newAreas[index].radius = e.target.value;
                                setFormData(prev => ({ ...prev, serviceAreas: newAreas }));
                              }}
                            />
                          </td>
                          <td>
                            <button
                              className="btn btn-danger btn-sm"
                              onClick={() => removeServiceArea(index)}
                            >
                              <i className="fa fa-trash"></i>
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>

                {/* Current Location Button */}
                <button
                  className="btn btn-secondary mb-3"
                  onClick={getCurrentLocation}
                >
                  <i className="fa fa-location-arrow me-2"></i>
                  Use My Location
                </button>
              </div>
            </div>
          </div>

          {/* Route Alerts Card */}
          <div className="col-md-6 mb-4">
            <div className="card">
              <div className="card-header d-flex justify-content-between align-items-center">
                <h4 className="card-title mb-0">Route Alerts</h4>
                <button 
                  className="btn btn-primary btn-sm"
                  onClick={addRouteAlert}
                >
                  Add Route
                </button>
              </div>
              <div className="card-body">
                <div className="table-responsive">
                  <table className="table table-hover">
                    <thead>
                      <tr>
                        <th>Origin ZIP</th>
                        <th>Destination ZIP</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {(formData.routeAlerts || []).map((route, index) => (
                        <tr key={index}>
                          <td>
                            <input
                              type="text"
                              className="form-control"
                              value={route.originZip}
                              onChange={(e) => {
                                const newRoutes = [...formData.routeAlerts];
                                newRoutes[index].originZip = e.target.value;
                                setFormData(prev => ({ ...prev, routeAlerts: newRoutes }));
                              }}
                              pattern="[0-9]{5}"
                              maxLength="5"
                            />
                          </td>
                          <td>
                            <input
                              type="text"
                              className="form-control"
                              value={route.destinationZip}
                              onChange={(e) => {
                                const newRoutes = [...formData.routeAlerts];
                                newRoutes[index].destinationZip = e.target.value;
                                setFormData(prev => ({ ...prev, routeAlerts: newRoutes }));
                              }}
                              pattern="[0-9]{5}"
                              maxLength="5"
                            />
                          </td>
                          <td>
                            <button
                              className="btn btn-danger btn-sm"
                              onClick={() => removeRouteAlert(index)}
                            >
                              <i className="fa fa-trash"></i>
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>

          {/* Alert Preferences Card */}
          <div className="col-md-6 mb-4">
            <div className="card">
              <div className="card-header">
                <h4 className="card-title mb-0">Alert Preferences</h4>
              </div>
              <div className="card-body">
                <div className="form-check mb-3">
                  <input
                    type="checkbox"
                    className="form-check-input"
                    id="rfpAlerts"
                    checked={formData.alertPreferences.rfpAlerts}
                    onChange={(e) => setFormData(prev => ({
                      ...prev,
                      alertPreferences: {
                        ...prev.alertPreferences,
                        rfpAlerts: e.target.checked
                      }
                    }))}
                  />
                  <label className="form-check-label" htmlFor="rfpAlerts">
                    RFP Job Alerts
                  </label>
                </div>
                <div className="form-check mb-3">
                  <input
                    type="checkbox"
                    className="form-check-input"
                    id="openTruckAlerts"
                    checked={formData.alertPreferences.openTruckAlerts}
                    onChange={(e) => setFormData(prev => ({
                      ...prev,
                      alertPreferences: {
                        ...prev.alertPreferences,
                        openTruckAlerts: e.target.checked
                      }
                    }))}
                  />
                  <label className="form-check-label" htmlFor="openTruckAlerts">
                    Open Truck Space Alerts
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Save Button */}
        <div className="row">
          <div className="col-12">
            <button 
              className="btn btn-primary btn-lg"
              onClick={handleSubmit}
            >
              Save All Settings
            </button>
          </div>
        </div>
      </div>

      <style jsx>{`
        .card {
          box-shadow: 0 4px 24px rgba(0, 0, 0, 0.06);
        }
        .card-header {
          background-color: transparent;
          border-bottom: 1px solid rgba(0, 0, 0, 0.125);
        }
        .form-label {
          font-weight: 500;
        }
        .form-check-label {
          cursor: pointer;
        }
      `}</style>
    </Applayout>
  );
}

export default Settings; 