import React, { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import axiosClient from '../../axios/axiosClient';
import Applayout from '../layout/applayout';
import Modal from '../../components/app/modal';

function ManageUsers() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedUser, setSelectedUser] = useState(null);
  const [pagination, setPagination] = useState({
    currentPage: 1,
    totalPages: 1,
    totalItems: 0
  });
  const [filters, setFilters] = useState({
    status: '',
    userType: '',
    search: '',
    page: 1,
    limit: 10
  });
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    fetchUsers();
  }, [filters]);

  const fetchUsers = async () => {
    try {
      setLoading(true);
      const queryParams = new URLSearchParams({
        ...filters,
        page: filters.page.toString(),
        limit: filters.limit.toString()
      }).toString();

      const response = await axiosClient.get(`/admin/users?${queryParams}`);
      if (response.data.status === 'success') {
        setUsers(response.data.data.users);
        setPagination(response.data.data.pagination);
      }
    } catch (error) {
      toast.error('Failed to fetch users');
    } finally {
      setLoading(false);
    }
  };

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    setFilters(prev => ({
      ...prev,
      [name]: value,
      page: 1 // Reset to first page on filter change
    }));
  };

  const handleStatusUpdate = async (userId, newStatus) => {
    try {
      const response = await axiosClient.patch(`/admin/users/${userId}/status`, {
        status: newStatus
      });
      
      if (response.data.status === 'success') {
        toast.success('User status updated successfully');
        fetchUsers();
      }
    } catch (error) {
      toast.error('Failed to update user status');
    }
  };

  const viewUserDetails = async (userId) => {
    try {
      const response = await axiosClient.get(`/admin/users/${userId}`);
      if (response.data.status === 'success') {
        const user = response.data.data.user;
        
        // Calculate stats from loads array
        const stats = {
          totalLoads: user.loads?.length || 0,
          activeLoads: user.loads?.filter(load => load.status === 'ACTIVE').length || 0,
          completedLoads: user.loads?.filter(load => load.status === 'COMPLETED').length || 0
        };

        setSelectedUser({ ...user, stats });
        setShowModal(true);
      }
    } catch (error) {
      toast.error('Failed to fetch user details');
    }
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <h3>Manage Users</h3>
            </div>
          </div>
        </div>

        {/* Filters */}
        <div className="card mb-4">
          <div className="card-body">
            <div className="row">
              <div className="col-md-3">
                <select 
                  className="form-select" 
                  name="status" 
                  value={filters.status}
                  onChange={handleFilterChange}
                >
                  <option value="">All Status</option>
                  <option value="active">Active</option>
                  <option value="inactive">Inactive</option>
                  <option value="pending">Pending</option>
                </select>
              </div>
              <div className="col-md-3">
                <select 
                  className="form-select" 
                  name="userType" 
                  value={filters.userType}
                  onChange={handleFilterChange}
                >
                  <option value="">All Types</option>
                  <option value="RFP">RFP Carrier</option>
                  <option value="RFD">RFD Carrier</option>
                  <option value="BROKER">Broker</option>
                </select>
              </div>
              <div className="col-md-6">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Search users..."
                  name="search"
                  value={filters.search}
                  onChange={handleFilterChange}
                />
              </div>
            </div>
          </div>
        </div>

        {/* Users Table */}
        <div className="card">
          <div className="card-body">
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Company</th>
                    <th>Type</th>
                    <th>Status</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {users.map(user => (
                    <tr key={user.id}>
                      <td>{`${user.firstName} ${user.lastName}`}</td>
                      <td>{user.email}</td>
                      <td>{user.companyName}</td>
                      <td>{user.userType}</td>
                      <td>
                        <span className={`badge bg-${
                          user.status === 'active' ? 'success' : 
                          user.status === 'inactive' ? 'danger' : 'warning'
                        }`}>
                          {user.status}
                        </span>
                      </td>
                      <td>
                        <button 
                          className="btn btn-primary btn-sm me-2"
                          onClick={() => viewUserDetails(user.id)}
                        >
                          View
                        </button>
                        <div className="dropdown d-inline">
                          <button 
                            className="btn btn-secondary btn-sm dropdown-toggle" 
                            type="button" 
                            data-bs-toggle="dropdown"
                          >
                            Status
                          </button>
                          <ul className="dropdown-menu">
                            <li>
                              <a 
                                className="dropdown-item" 
                                href="#"
                                onClick={() => handleStatusUpdate(user.id, 'active')}
                              >
                                Activate
                              </a>
                            </li>
                            <li>
                              <a 
                                className="dropdown-item" 
                                href="#"
                                onClick={() => handleStatusUpdate(user.id, 'inactive')}
                              >
                                Deactivate
                              </a>
                            </li>
                            <li>
                              <a 
                                className="dropdown-item" 
                                href="#"
                                onClick={() => handleStatusUpdate(user.id, 'suspended')}
                              >
                                Suspend
                              </a>
                            </li>
                          </ul>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            {/* Pagination */}
            <div className="d-flex justify-content-between align-items-center mt-4">
              <div>
                Showing {users.length} of {pagination.totalItems} users
              </div>
              <nav>
                <ul className="pagination">
                  {[...Array(pagination.totalPages)].map((_, index) => (
                    <li 
                      key={index} 
                      className={`page-item ${pagination.currentPage === index + 1 ? 'active' : ''}`}
                    >
                      <button
                        className="page-link"
                        onClick={() => setFilters(prev => ({ ...prev, page: index + 1 }))}
                      >
                        {index + 1}
                      </button>
                    </li>
                  ))}
                </ul>
              </nav>
            </div>
          </div>
        </div>

        {/* User Details Modal */}
        <Modal
          show={showModal}
          onClose={() => {
            setShowModal(false);
            setSelectedUser(null);
          }}
          title="User Details"
        >
          {selectedUser && (
            <div className="row">
              <div className="col-md-6">
                <h5>Personal Information</h5>
                <p><strong>Name:</strong> {`${selectedUser.firstName} ${selectedUser.lastName}`}</p>
                <p><strong>Email:</strong> {selectedUser.email}</p>
                <p><strong>Phone:</strong> {selectedUser.phone}</p>
                <p><strong>Website:</strong> {selectedUser.website}</p>
              </div>
              <div className="col-md-6">
                <h5>Account Information</h5>
                <p><strong>Company:</strong> {selectedUser.companyName}</p>
                <p><strong>User Type:</strong> {selectedUser.userType}</p>
                <p><strong>Status:</strong> {selectedUser.status}</p>
                <p><strong>DOT Number:</strong> {selectedUser.dotNumber || 'N/A'}</p>
              </div>
              <div className="col-12 mt-4">
                <h5>Load Statistics</h5>
                <div className="row">
                  <div className="col-md-4">
                    <div className="card">
                      <div className="card-body text-center">
                        <h3>{selectedUser.stats.totalLoads}</h3>
                        <p>Total Loads</p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="card">
                      <div className="card-body text-center">
                        <h3>{selectedUser.stats.activeLoads}</h3>
                        <p>Active Loads</p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="card">
                      <div className="card-body text-center">
                        <h3>{selectedUser.stats.completedLoads}</h3>
                        <p>Completed Loads</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </Modal>
      </div>
    </Applayout>
  );
}

export default ManageUsers; 