import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosClient from '../../axios/axiosClient';
import { toast } from 'react-toastify';

const RFDCarrierDashboard = () => {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [stats, setStats] = useState({
        openTrucks: 0,
        availableRFDLoads: 0,
        activeDeliveries: 0,
        revenue: 0
    });

    const [myTrucks, setMyTrucks] = useState([]);
    const [availableRFDLoads, setAvailableRFDLoads] = useState([]);

    useEffect(() => {
        fetchDashboardData();
    }, []);

    const fetchDashboardData = async () => {
        try {
            const response = await axiosClient.get('/dashboard/rfd-carrier');
            const { stats: dashboardStats, myTrucks: trucks, availableRFDLoads: loads } = response.data.data;
            setStats(dashboardStats);
            setMyTrucks(trucks);
            setAvailableRFDLoads(loads);
        } catch (error) {
            toast.error('Failed to fetch dashboard data');
        } finally {
            setLoading(false);
        }
    };

    if (loading) {
        return <div className="loader-wrapper">
            <div className="loader-index"><span></span></div>
        </div>;
    }

    return (
        <div className="container-fluid dashboard-container p-4">
            {/* Stats Row */}
            <div className="row mb-4">
                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-primary b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-truck text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Open Trucks</span>
                                    <h4 className="mb-0 counter text-white">{stats.openTrucks}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-warning b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-search text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Available RFD Loads</span>
                                    <h4 className="mb-0 counter text-white">{stats.availableRFDLoads}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-success b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-route text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Active Deliveries</span>
                                    <h4 className="mb-0 counter text-white">{stats.activeDeliveries}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-info b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-dollar-sign text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Total Revenue</span>
                                    <h4 className="mb-0 counter text-white">${stats.revenue.toLocaleString()}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* My Trucks and Available RFD Loads */}
            <div className="row">
                <div className="col-md-6 mb-4">
                    <div className="card h-100">
                        <div className="card-header d-flex justify-content-between align-items-center">
                            <h5 className="mb-0">My Trucks</h5>
                            <button 
                                className="btn btn-primary btn-sm"
                                onClick={() => navigate('/post-loads')}
                            >
                                Post New Truck
                            </button>
                        </div>
                        <div className="card-body">
                            <div className="table-responsive">
                                <table className="table table-hover">
                                    <thead>
                                        <tr>
                                            <th>Location</th>
                                            <th>Avail. Date</th>
                                            <th>Space</th>
                                            <th>Status</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {myTrucks.map(truck => (
                                            <tr key={truck.id}>
                                                <td>{truck.location}</td>
                                                <td>{new Date(truck.availableDate).toLocaleDateString()}</td>
                                                <td>
                                                    <span>
                                                        {truck.cubicFeet}cf
                                                    </span>
                                                </td>
                                                <td>
                                                    <span className={`badge bg-${truck.status === 'Available' ? 'success' : 'warning'}`}>
                                                        {truck.status}
                                                    </span>
                                                </td>
                                                <td>
                                                    <button 
                                                        className="btn btn-primary btn-sm"
                                                        onClick={() => navigate(`/view-requests/${truck.id}`)}
                                                    >
                                                        View
                                                    </button>
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-md-6 mb-4">
                    <div className="card h-100">
                        <div className="card-header d-flex justify-content-between align-items-center">
                            <h5 className="mb-0">Available RFD Loads</h5>
                            <button 
                                className="btn btn-primary btn-sm"
                                onClick={() => navigate('/view-loads')}
                            >
                                View All
                            </button>
                        </div>
                        <div className="card-body">
                            <div className="table-responsive">
                                <table className="table table-hover">
                                    <thead>
                                        <tr>
                                            <th>Route</th>
                                            <th>Rate</th>
                                            <th>Space</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {availableRFDLoads.map(load => (
                                            <tr key={load.id}>
                                                <td>{load.origin}  {load.destination}</td>
                                                <td>${load.rate.toLocaleString()}</td>
                                                <td>
                                                    <span>
                                                        {load.cubicFeet}cf
                                                    </span>
                                                </td>
                                                <td>
                                                    <button 
                                                        className="btn btn-primary btn-sm"
                                                        onClick={() => navigate(`/view-requests/${load.id}`)}
                                                    >
                                                        View
                                                    </button>
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default RFDCarrierDashboard; 