import React, { useState, useEffect } from "react";
import Applayout from "./layout/applayout";
import { toast } from "react-toastify";
import Modal from "../components/app/modal";
import loadService from "../services/loadService";
import { useNavigate } from "react-router-dom";
import LoadStatusUpdate from "../components/LoadStatusUpdate";

function AllLoads() {
  const [loads, setLoads] = useState([]);
  const [selectedLoad, setSelectedLoad] = useState(null);
  const [modalTitle, setModalTitle] = useState("Load Details");
  const [loading, setLoading] = useState(false);
  const [filters, setFilters] = useState({
    origin: "",
    destination: "",
    startDate: "",
    endDate: "",
    type: "all",
  });
  const [requestData, setRequestData] = useState({
    proposedRate: '',
    message: "I'm interested in this load. Available with a 53' dry van and can pick up on schedule."
  });
  const navigate = useNavigate();

  useEffect(() => {
    fetchLoads();
  }, []);

  const fetchLoads = async () => {
    setLoading(true);
    try {
      const response = await loadService.allLoads();
      if (response.status === "success") {
        setLoads(response.data);
      }
    } catch (error) {
      toast.error(error.message || "Failed to fetch loads");
    } finally {
      setLoading(false);
    }
  };

  const handleFilterChange = (e) => {
    const { name, value } = e.target;
    console.log(name, value, "tested")
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSearch = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      const response = await loadService.viewLoads(filters);
      if (response.status === "success") {
        setLoads(response.data);
      }
    } catch (error) {
      toast.error("Failed to search loads");
    } finally {
      setLoading(false);
    }
  };

  const handleViewLoad = (load) => {
    setSelectedLoad(load);
    setModalTitle(`Load Details - ${load.jobNumber}`);
  };

  const formatDate = (dateString) => {
    if (!dateString) return "N/A";
    return new Date(dateString).toLocaleString();
  };

  const handleRequestLoad = async (jobNumber) => {
    try {
      setLoading(true);
      await loadService.requestLoad(jobNumber, requestData);
      toast.success('Load requested successfully');
      //navigate(`/view-requests/${jobNumber}`);
    } catch (error) {
      toast.error(error.message || 'Failed to request load');
    } finally {
      setLoading(false);
    }
  };

  const clearFilters = () => {
    setFilters({
      origin: "",
      destination: "",
      startDate: "",
      endDate: "",
      status: "",
    });
  };

  const handleStatusUpdate = (loadId, newStatus) => {
    setLoads((prevLoads) =>
      prevLoads.map((load) =>
        load.id === loadId ? { ...load, status: newStatus } : load
      )
    );
  };
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setRequestData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <div className="page-header-left">
                <h3>View Loads</h3>
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <i className="fa fa-home"></i>
                  </li>
                  <li className="breadcrumb-item active">View Loads</li>
                </ol>
              </div>
            </div>
          </div>
        </div>

        <div className="card">
          <div className="card-header">
            <h4 className="card-title mb-0">Search Loads</h4>
          </div>
          <div className="card-body">
            <form onSubmit={handleSearch}>
              <div className="row g-3">
                <div className="col-md-3">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Origin"
                    name="origin"
                    value={filters.origin}
                    onChange={handleFilterChange}
                  />
                </div>
                <div className="col-md-3">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Destination"
                    name="destination"
                    value={filters.destination}
                    onChange={handleFilterChange}
                  />
                </div>
                <div className="col-md-2">
                  <select
                    className="form-select"
                    name="type"
                    value={filters.type}
                    onChange={handleFilterChange}
                  >
                    <option value="">All Type</option>
                    <option value="RFP">Ready for Pickup</option>
                    <option value="RFD">Ready for Delivery</option>
                    <option value="TRUCK">Truck Space</option>
                  </select>
                </div>
                <div className="col-md-4">
                  <button
                    type="submit"
                    className="btn btn-primary me-2"
                    disabled={loading}
                  >
                    {loading ? "Searching..." : "Search"}
                  </button>

                  <button
                    className="btn btn-outline-secondary btn-sm me-2"
                    onClick={clearFilters}
                  >
                    <i className="fa fa-times me-1"></i>
                    Clear Filters
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>

        <div className="card mt-4">
          <div className="card-body">
            <div className="table-responsive">
              <table className="table table-hover">
                <thead>
                  <tr>
                    <th>Job Number</th>
                    <th>Type</th>
                    <th>Location</th>
                    <th>Dates</th>
                    <th>Rate</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {loads.map((load) => (
                    <tr key={load.id}>
                      <td>{load.jobNumber}</td>
                      <td>{load.type}</td>
                      <td>
                        <small className="d-block">
                          {load.originLocation} 
                        </small>
                        <small>{load.destinationLocation}</small>
                      </td>
                      <td>
                        <small className="d-block">
                          Pickup: {formatDate(load.pickupDate)}
                        </small>
                        <small>Delivery: {formatDate(load.deliveryDate)}</small>
                      </td>
                      <td>${parseFloat(load.rate).toLocaleString()}</td>
                      <td>
                        <button
                          className="btn btn-primary btn-sm"
                          onClick={() => handleViewLoad(load)}
                          data-bs-toggle="modal"
                          data-bs-target="#loadDetailsModal"
                        >
                          <i className="fa fa-eye"></i> details
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        <Modal title={modalTitle} modalId="loadDetailsModal" modalSize="lg">
          {selectedLoad && (
            <div className="row">
              <div className="col-md-6">
                <div className="card mb-3">
                  <div className="card-body">
                    <div className="table-responsive">
                      <h5 className="text-primary mb-4">Basic Information</h5>
                      <table className="table table-borderless mb-0">
                        <tbody>
                          <tr>
                            <td width="40%">
                              <strong>Job Number:</strong>
                            </td>
                            <td>{selectedLoad.jobNumber}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Type:</strong>
                            </td>
                            <td>{selectedLoad.type}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Status:</strong>
                            </td>
                            <td>
                              <span
                                className={`badge bg-${
                                  selectedLoad.status === "ACTIVE"
                                    ? "success"
                                    : "warning"
                                }`}
                              >
                                {selectedLoad.status}
                              </span>
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Created:</strong>
                            </td>
                            <td>{formatDate(selectedLoad.createdAt)}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>

                <div className="card">
                  <div className="card-body">
                    <div className="table-responsive">
                      <h5 className="text-primary mb-4">Route Information</h5>
                      <table className="table table-borderless mb-0">
                        <tbody>
                          <tr>
                            <td width="40%">
                              <strong>Company:</strong>
                            </td>
                            <td>{selectedLoad.contact.company}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Contact Name:</strong>
                            </td>
                            <td>{selectedLoad.contact.name}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Phone:</strong>
                            </td>
                            <td>{selectedLoad.contact.phone}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Email:</strong>
                            </td>
                            <td>{selectedLoad.contact.email}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-md-6">
                <div className="card mb-3">
                  <div className="card-body">
                    <div className="table-responsive">
                      <h5 className="text-primary mb-4">Load Details</h5>
                      <table className="table table-borderless mb-0">
                        <tbody>
                          <tr>
                            <td width="40%">
                              <strong>Origin:</strong>
                            </td>
                            <td>{selectedLoad.originLocation}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Origin Zip:</strong>
                            </td>
                            <td>{selectedLoad.originZip}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Destination:</strong>
                            </td>
                            <td>{selectedLoad.destinationLocation}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Destination Zip:</strong>
                            </td>
                            <td>{selectedLoad.destinationZip}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Pickup Date:</strong>
                            </td>
                            <td>{formatDate(selectedLoad.pickupDate)}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Delivery Date:</strong>
                            </td>
                            <td>{formatDate(selectedLoad.deliveryDate)}</td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Weight:</strong>
                            </td>
                            <td>
                              {parseFloat(selectedLoad.weight).toLocaleString()}{" "}
                              lbs
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Rate:</strong>
                            </td>
                            <td>
                              ${parseFloat(selectedLoad.rate).toLocaleString()}
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Cubic Feet:</strong>
                            </td>
                            <td>
                              $
                              {parseFloat(
                                selectedLoad.cubicFeet
                              ).toLocaleString()}
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Equipment Type:</strong>
                            </td>
                            <td className="text-capitalize">
                              {selectedLoad.equipmentType}
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <strong>Load Type:</strong>
                            </td>
                            <td className="text-capitalize">
                              {selectedLoad.type}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>

              <div className="col-12 mt-4">
                <hr />
                <h5 className="text-primary mb-4">Submit Request</h5>
                <div className="row">
                  <div className="col-md-6 mb-3">
                    <label className="form-label">Proposed Rate ($)</label>
                    <input
                      type="number"
                      className="form-control"
                      name="proposedRate"
                      value={requestData.proposedRate}
                      onChange={handleInputChange}
                      placeholder="Enter your proposed rate"
                      required
                    />
                  </div>
                  <div className="col-6 mb-3">
                    <label className="form-label">Message</label>
                    <textarea
                      className="form-control"
                      name="message"
                      value={requestData.message}
                      onChange={handleInputChange}
                      rows="3"
                      placeholder="Enter additional details or requirements"
                      required
                    />
                  </div>
                  <div className="col-12">
                    <button
                      className="btn btn-primary w-100"
                      onClick={() => handleRequestLoad(selectedLoad.id)}
                      disabled={loading}
                    >
                      {loading ? (
                        <span>
                          <i className="fa fa-spinner fa-spin me-2"></i>
                          Submitting Request...
                        </span>
                      ) : (
                        <span>
                          <i className="fa fa-paper-plane me-2"></i>
                          Submit Load Request
                        </span>
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}
        </Modal>
      </div>
    </Applayout>
  );
}

export default AllLoads;
