import axios from "axios";

const axiosClient = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL || 'https://3.22.81.50/:9000/api',
    headers: {
        'Content-Type': 'application/json',
        'ngrok-skip-browser-warning':"true"
    }
});

axiosClient.interceptors.request.use(
    (config) => {
        const token = localStorage.getItem('ACCESS_TOKEN');
        if (token) {
            config.headers.Authorization = `Bearer ${token}`;
        }
        return config;
    },
    (error) => {
        console.error("Request configuration error:", error.message);
        return Promise.reject({
            message: "Failed to setup request",
            error: error
        });
    }
);

axiosClient.interceptors.response.use(
    (response) => {
        return response;
    },
    (error) => {
        let errorMessage = "An unexpected error occurred";

        if (error.code === "ERR_NETWORK") {
            errorMessage = "Unable to connect to the server. Please check your connection or try again later.";
            console.error("Network Error:", error);
        } else if (error.response) {
            switch (error.response.status) {
                case 401:
                    errorMessage = "Unauthorized access. Please login again.";
                    localStorage.removeItem("ACCESS_TOKEN");
                    if (!window.location.pathname.includes('/login')) {
                        window.location.href = "/login";
                    }
                    break;
                
                case 403:
                    errorMessage = "Access forbidden. You don't have permission for this action.";
                    break;
                
                case 404:
                    errorMessage = "Resource not found.";
                    break;
                
                case 422:
                    errorMessage = error.response.data.message || "Validation error occurred.";
                    break;
                
                case 500:
                    errorMessage = "Server error. Please try again later.";
                    break;
                
                default:
                    errorMessage = error.response.data.message || "Something went wrong.";
            }
            console.error("Server Error:", error.response.data);
        } else if (error.request) {
            errorMessage = "No response from server. Please try again.";
            console.error("No Response Error:", error.request);
        }

        return Promise.reject({
            message: errorMessage,
            originalError: error,
            statusCode: error.response?.status
        });
    }
);

export default axiosClient;
