import React, { useContext, useEffect, useState } from 'react'
import SideBarTriggerContext from '../../../context/SideBarTriggerContext';
import AuthContext from '../../../context/AuthContext';
import { Link } from 'react-router-dom';
import Notification from './notification';
import axiosClient from '../../../axios/axiosClient';
import fdoLogo from "../../../assets/images/fdo.png"
import SearchLoad from './searchLoad';
import SyncLoadboardButton from '../../../components/SyncLoadboardButton';
import { getImageUrl } from '../../../utils/imageHelper';
import NotificationIndicator from '../../../components/NotificationIndicator';

function Mainheader() {
    const { triggerSideBar, setTriggerSideBar } = useContext(SideBarTriggerContext);
    const { logout, user } = useContext(AuthContext);
    const [loading, setLoading] = useState([]);
    const [search, setSearch] = useState([]);
    const [toggleMenu, setToggleMenu] = useState(false);
    const [searchResult, setSearchResult] = useState([]);

    const searchLoad = () =>{
      setLoading(true);
      axiosClient
      .get(`/beneficiaries/search?search=${search}`)
      .then((response) => {
        setSearchResult(response.data.data)
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
      });
    }

    const handleKeyDown = (e) => {
      if (e.key === "Enter") {
        e.preventDefault();  
        searchLoad()
      }
    };

    useEffect(() => {
      // if(search.length > 0)
      // searchLoad()
    }, [search])

    return (
        <div className={`page-main-header ${triggerSideBar ? 'open' : ''}`}>
        <div className="main-header-right row">
          <div className="main-header-left col-auto px-0 d-lg-none">
            <div className="logo-wrapper"><Link href="/"><img src={fdoLogo} alt="" /></Link></div>
          </div>
          <div className="vertical-mobile-sidebar col-auto ps-3 d-none"><i className="fa fa-bars sidebar-bar"></i></div>
          <div className="mobile-sidebar col-auto ps-0 d-block">
            <div className="media-body switch-sm" onClick={()=>setTriggerSideBar(!triggerSideBar)}>
              <label className="switch"><Link ><i id="sidebar-toggle" data-feather="align-left"></i></Link></label>
            </div>
          </div>
          <div className="nav-right col p-0">
            <ul className={`nav-menus ${toggleMenu ?'open':''}`}>
              <SearchLoad />
              <li className="d-flex align-items-center">
                <SyncLoadboardButton className="mx-2" />
              </li>
              <li className="onhover-dropdown">
                <Link to="/notifications" className="media align-items-center position-relative">
                  <i data-feather="bell"></i>
                  <NotificationIndicator />
                </Link>
              </li>
              <li className="onhover-dropdown">
                <div className="media align-items-center">
                  <img className="align-self-center pull-right img-50 rounded-circle" src={getImageUrl(user?.photo)} alt="header-user" />
                </div>
                <ul className="profile-dropdown onhover-show-div p-20">
                  <li><Link to="/user-profile"><i data-feather="user"></i>Edit Profile</Link></li>

                  <li><Link href="#" onClick={()=> logout()}><i data-feather="log-out"></i>Logout</Link></li>
                </ul>
              </li>
            </ul>
            <div className="d-lg-none mobile-toggle pull-right" onClick={() => setToggleMenu(!toggleMenu)}><i data-feather="more-horizontal"></i></div>
          </div>
          <script id="result-template" type="text/x-handlebars-template">
            <div className="ProfileCard u-cf d-none">                        
            <div className="ProfileCard-avatar"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" className="feather feather-airplay m-0"><path d="M5 17H4a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h16a2 2 0 0 1 2 2v10a2 2 0 0 1-2 2h-1"></path><polygon points="12 15 17 21 7 21 12 15"></polygon></svg></div>
            <div className="ProfileCard-details">
            <div  className="ProfileCard-realName"></div>
            </div>
            </div>
          </script>
          <script id="empty-template" type="text/x-handlebars-template">
            {searchResult.length === 0 && <div className="EmptyMessage">Your search turned up 0 results. </div>}
          </script>
        </div>
      </div>
    )
}

export default Mainheader
