import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axiosClient from '../../axios/axiosClient';
import { toast } from 'react-toastify';

const RFPCarrierDashboard = () => {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(true);
    const [stats, setStats] = useState({
        availableLoads: 0,
        postedRFDLoads: 0,
        activeDeliveries: 0,
        revenue: 0
    });

    const [availableLoads, setAvailableLoads] = useState([]);
    const [myRFDLoads, setMyRFDLoads] = useState([]);

    useEffect(() => {
        fetchDashboardData();
    }, []);
    const formatDate = (dateString) => {
        if (!dateString) return "N/A";
        return new Date(dateString).toLocaleString();
      };

    const fetchDashboardData = async () => {
        try {
            const response = await axiosClient.get('/dashboard/rfp-carrier');
            console.log(response.data, "responseresponse")
            const { stats: dashboardStats, availableLoads: loads, myRFDLoads: rfdLoads } = response.data.data;
            setStats(dashboardStats);
            setAvailableLoads(loads);
            setMyRFDLoads(rfdLoads);
        } catch (error) {
            toast.error('Failed to fetch dashboard data');
        } finally {
            setLoading(false);
        }
    };

    if (loading) {
        return <div className="loader-wrapper">
            <div className="loader-index"><span></span></div>
        </div>;
    }

    return (
        <div className="container-fluid dashboard-container p-4">
            {/* Stats Row */}
            <div className="row mb-4">
                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-primary b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-search text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Available Loads</span>
                                    <h4 className="mb-0 counter text-white">{stats.availableLoads}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-success b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-truck text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Posted RFD Loads</span>
                                    <h4 className="mb-0 counter text-white">{stats.postedRFDLoads}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-warning b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-route text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Active Deliveries</span>
                                    <h4 className="mb-0 counter text-white">{stats.activeDeliveries}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-sm-6 col-xl-3">
                    <div className="card o-hidden">
                        <div className="bg-info b-r-4 card-body">
                            <div className="d-flex static-top-widget">
                                <div className="align-self-center text-center">
                                    <i className="fa fa-dollar-sign text-white fa-2x me-3"></i>
                                </div>
                                <div className="flex-grow-1">
                                    <span className="m-0 text-white">Total Revenue</span>
                                    <h4 className="mb-0 counter text-white">${stats.revenue.toLocaleString()}</h4>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Available Loads and My RFD Loads */}
            <div className="row">
                <div className="col-md-6 mb-4">
                    <div className="card h-100">
                        <div className="card-header d-flex justify-content-between align-items-center">
                            <h5 className="mb-0">Available Broker Loads</h5>
                            <button 
                                className="btn btn-primary btn-sm"
                                onClick={() => navigate('/search-loads')}
                            >
                                View All
                            </button>
                        </div>
                        <div className="card-body">
                            <div className="table-responsive">
                                <table className="table table-hover">
                                    <thead>
                                        <tr>
                                            <th>Broker</th>
                                            <th>Route</th>
                                            <th>Dates</th>
                                            <th>Rate</th>
                                            {/* <th>Action</th> */}
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {availableLoads.map(load => (
                                            <tr key={load.id}>
                                                <td>
                                                    {load.user.firstName} {load.user.lastName}
                                                    <br />
                                                    <small>({load.user.companyName})</small>
                                                </td>
                                                <td>{load.pickupLocation}  {load.deliveryLocation}</td>
                                                <td>
                                                    <small className="d-block">Pickup: {formatDate(load.pickupDate)}</small>
                                                    <small>Delivery: {formatDate(load.deliveryDate)}</small>
                                                </td>
                                                <td>${load.rate.toLocaleString()}</td>
                                                {/* <td>
                                                    <button 
                                                        className="btn btn-primary btn-sm"
                                                        onClick={() => navigate(`/view-loads/${load.id}`)}
                                                    >
                                                        View
                                                    </button>
                                                </td> */}
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-md-6 mb-4">
                    <div className="card h-100">
                        <div className="card-header d-flex justify-content-between align-items-center">
                            <h5 className="mb-0">Available Truck Space</h5>
                            <button 
                                className="btn btn-primary btn-sm"
                                onClick={() => navigate('/post-loads')}
                            >
                                Post RFD Load
                            </button>
                        </div>
                        <div className="card-body">
                            <div className="table-responsive">
                                <table className="table table-hover">
                                    <thead>
                                        <tr>
                                            <th>Route</th>
                                            <th>Space</th>
                                            <th>Status</th>
                                            <th>Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {myRFDLoads.map(load => (
                                            <tr key={load.id}>
                                                <td>{load.pickupLocation}  {load.deliveryLocation}</td>
                                                <td>
                                                    <span>
                                                        {load.cubicFeet}cf
                                                    </span>
                                                </td>
                                                <td>
                                                    <span className={`badge bg-${load.status === 'Active' ? 'success' : 'warning'}`}>
                                                        {load.status}
                                                    </span>
                                                </td>
                                                <td>
                                                    <button 
                                                        className="btn btn-primary btn-sm"
                                                        onClick={() => navigate(`/view-requests/${load.id}`)}
                                                    >
                                                        View
                                                    </button>
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default RFPCarrierDashboard; 