import axiosClient from '../axios/axiosClient';

const loadService = {
    createLoad: async (loadData) => {
        
        try {
            const response = await axiosClient.post('/loads', loadData);
            return response.data;
        } catch (error) {
            if (error.code === "ERR_NETWORK") {
                throw {
                    message: "Unable to connect to the server. Please check if the server is running.",
                    statusCode: 'NETWORK_ERROR'
                };
            }
            
            throw {
                message: error.message || 'Failed to create load',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    getLoads: async (params) => {
        try {
            const response = await axiosClient.get('/loads', { params });
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to fetch loads',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },
    viewLoads: async (params) => {
        try {
            const response = await axiosClient.get('/loads/view-loads', { params });
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to fetch loads',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    allLoads: async (params) => {
        try {
            const response = await axiosClient.get('/loads/all-loads', { params });
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to fetch loads',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    getLoadById: async (id) => {
        try {
            const response = await axiosClient.get(`/loads/${id}`);
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to fetch load details',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    updateLoad: async (id, loadData) => {
        try {
            const response = await axiosClient.put(`/loads/${id}`, loadData);
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to update load',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    deleteLoad: async (id) => {
        try {
            const response = await axiosClient.delete(`/loads/${id}`);
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to delete load',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    searchLoads: async (params) => {
        const queryString = new URLSearchParams(params).toString();
        const response = await axiosClient.get(`/loads/search?${queryString}`);
        return response.data;
    },

    requestLoad: async (jobNumber, requestData) => {
        try {
            const response = await axiosClient.post(`/loads/${jobNumber}/request`, requestData);
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to request load',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    getLoadRequests: async (loadId) => {
        try {
            const response = await axiosClient.get(`/loads/${loadId}/requests`);
            return response.data;
        } catch (error) {
            throw {
                message: error.message || 'Failed to fetch load requests',
                statusCode: error.statusCode || 'ERROR'
            };
        }
    },

    updateRequestStatus: async (requestId, status, responseMessage) => {
        try {
            const response = await axiosClient.patch(`/requests/${requestId}/status`, {
                status,
                responseMessage
            });
            return response.data;
        } catch (error) {
            throw {
                message: error.message || `Failed to ${status.toLowerCase()} request`,
                statusCode: error.statusCode || 'ERROR'
            };
        }
    }
};

export default loadService;