import React, { useState } from 'react'
import axiosClient from '../../axios/axiosClient';
import logo from "../../assets/images/fdo.png"
import Loader from '../../components/app/loader';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import LoadBoardLinks from '../../components/LoadBoardLinks';

function Register() {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const [formData, setFormData] = useState({
        firstName: '',
        lastName: '',
        username: '',
        password: '',
        companyName: '',
        userType: '',
        dotNumber: '',
        contactName: '',
        phone: '',
        email: '',
        website: '',
        loadBoardUrls: []
    });

    const userTypes = [
        { value: 'BROKER', label: 'Broker - Post loads ready for pickup' },
        { value: 'RFP_CARRIER', label: 'RFP Carrier - Find and post RFD loads' },
        { value: 'RFD_CARRIER', label: 'RFD Carrier - Post open truck space and find RFP loads' }
    ];

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
    };

    const handleLoadBoardUrlsChange = (urls) => {
        setFormData(prev => ({
            ...prev,
            loadBoardUrls: urls
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);

        try {
            const response = await axiosClient.post('/auth/register', formData);
            toast.success('Registration Successful.');
            navigate('/login');
        } catch (error) {
            console.error(error);
            toast.error(error.response?.data?.message || 'Registration failed');
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <div className="container-fluid">
                <div className="authentication-main">
                    <div className="row">
                        <div className="col-sm-12 p-0">
                            <div className="auth-innerright">
                                <div className="authentication-box">
                                    <div className="text-center">
                                        <img src={logo} alt="" style={{height:"100px"}} />
                                    </div>
                                    <div className="card mt-4 p-4">
                                        <h4 className="text-center">NEW USER</h4>
                                        <h6 className="text-center mb-4">Complete the form below to register</h6>
                                        <form className="theme-form" onSubmit={handleSubmit}>
                                            <div className="row g-2">
                                                {/* User Type Selection */}
                                                <div className="col-12 mb-3">
                                                    <label className="col-form-label">User Type *</label>
                                                    <select 
                                                        className="form-select"
                                                        name="userType"
                                                        value={formData.userType}
                                                        onChange={handleChange}
                                                        required
                                                    >
                                                        <option value="">Select User Type</option>
                                                        {userTypes.map(type => (
                                                            <option key={type.value} value={type.value}>
                                                                {type.label}
                                                            </option>
                                                        ))}
                                                    </select>
                                                </div>
                                                {/* Company Information */}
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Company Name *</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="text" 
                                                            name="companyName"
                                                            value={formData.companyName}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">DOT Number (7 digits)</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="text" 
                                                            name="dotNumber"
                                                            value={formData.dotNumber}
                                                            onChange={handleChange}
                                                            maxLength="7"
                                                            pattern="\d{7}"
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Contact Name *</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="text" 
                                                            name="contactName"
                                                            value={formData.contactName}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Phone Number *</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="tel" 
                                                            name="phone"
                                                            value={formData.phone}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Email *</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="email" 
                                                            name="email"
                                                            value={formData.email}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Website</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="url" 
                                                            name="website"
                                                            value={formData.website}
                                                            onChange={handleChange}
                                                            placeholder="https://"
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">First Name</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="text" 
                                                            name="firstName"
                                                            value={formData.firstName}
                                                            onChange={handleChange}
                                                            placeholder="John"
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Last Name</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="text" 
                                                            name="lastName"
                                                            value={formData.lastName}
                                                            onChange={handleChange}
                                                            placeholder="Doe"
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Username *</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="text" 
                                                            name="username"
                                                            value={formData.username}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="col-md-6">
                                                    <div className="mb-3">
                                                        <label className="col-form-label">Password *</label>
                                                        <input 
                                                            className="form-control" 
                                                            type="password" 
                                                            name="password"
                                                            value={formData.password}
                                                            onChange={handleChange}
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                            </div>

                                            {/* Load Board Links */}
                                            {/* <div className="mb-4">
                                                <LoadBoardLinks
                                                    loadBoardUrls={formData.loadBoardUrls}
                                                    onChange={handleLoadBoardUrlsChange}
                                                />
                                            </div> */}

                                            <div className="row g-2">
                                                <div className="col-sm-4">
                                                    <button 
                                                        className="btn btn-primary w-100" 
                                                        type="submit"
                                                        disabled={loading}
                                                    >
                                                        {loading ? 'Signing Up...' : 'Sign Up'}
                                                    </button>
                                                </div>
                                                <div className="col-sm-8">
                                                    <div className="text-start mt-2 m-l-20">
                                                        Already a user?{" "}
                                                        <a 
                                                            className="btn-link text-capitalize" 
                                                            onClick={() => navigate('/login')}
                                                            style={{ cursor: 'pointer' }}
                                                        >
                                                            Login
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {loading && <Loader />}
        </>
    )
}

export default Register;
