import React, { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import axiosClient from '../axios/axiosClient';

const SyncLoadboardButton = ({ className = '' }) => {
  const [showButton, setShowButton] = useState(false);

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem('user'));
    if (user && user.loadBoardUrls && user.loadBoardUrls.length > 0) {
      setShowButton(true);
    }
  }, []);

  const handleSyncLoadboards = async () => {
    try {
      const response = await axiosClient.get('/loadboard/scrape-save');
      if (response.data.status === 'success') {
        toast.success('Loadboard sync completed successfully');
      }
    } catch (error) {
      toast.error(error.response?.data?.message || 'Failed to sync loadboards');
    }
  };

  if (!showButton) return null;

  return (
    <button 
      className={`btn btn-primary btn-sm ${className}`}
      onClick={handleSyncLoadboards}
    >
      <i className="fa fa-sync me-2"></i>
      Sync Loadboards
    </button>
  );
};

export default SyncLoadboardButton; 