import React, { useState, useEffect } from 'react';
import { toast } from 'react-toastify';
import axiosClient from '../../axios/axiosClient';
import Applayout from '../layout/applayout';

function LoadboardScraper() {
  const [loading, setLoading] = useState(false);
  const [scrapedData, setScrapedData] = useState([]);
  const [loadboardUrls, setLoadboardUrls] = useState([]);

  useEffect(() => {
    fetchLoadboardUrls();
  }, []);

  const fetchLoadboardUrls = async () => {
    try {
      const response = await axiosClient.get('/loadboard/scrape');
      if (response.data.status === 'success') {
        setLoadboardUrls(response.data.data);
      }
    } catch (error) {
      toast.error('Failed to fetch loadboard URLs');
    }
  };

  const handleScrape = async () => {
    try {
      setLoading(true);
      const response = await axiosClient.get('/loadboard/scrape');
      
      if (response.data.status === 'success') {
        setScrapedData(response.data.data);
        toast.success('Scraping completed successfully');
      }
    } catch (error) {
      toast.error(error.response?.data?.message || 'Failed to scrape loadboard');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row align-items-center">
            <div className="col">
              <h3>Loadboard Scraper</h3>
            </div>
            <div className="col-auto">
              <button 
                className="btn btn-primary"
                onClick={handleScrape}
                disabled={loading}
              >
                {loading ? (
                  <><i className="fa fa-spinner fa-spin me-2"></i>Scraping...</>
                ) : (
                  <><i className="fa fa-sync me-2"></i>Start Scraping</>
                )}
              </button>
            </div>
          </div>
        </div>

        {/* Loadboard URLs Card */}
        <div className="card mb-4">
          <div className="card-header">
            <h5 className="mb-0">Configured Loadboard URLs</h5>
          </div>
          <div className="card-body">
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th>URL</th>
                    <th>Last Scraped</th>
                    <th>Status</th>
                  </tr>
                </thead>
                <tbody>
                  {loadboardUrls.map((url, index) => (
                    <tr key={index}>
                      <td>{url.url}</td>
                      <td>{new Date(url.lastScraped).toLocaleString()}</td>
                      <td>
                        <span className={`badge bg-${url.status === 'active' ? 'success' : 'warning'}`}>
                          {url.status}
                        </span>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        {/* Scraped Data Card */}
        <div className="card">
          <div className="card-header">
            <h5 className="mb-0">Scraped Loads</h5>
          </div>
          <div className="card-body">
            <div className="table-responsive">
              <table className="table">
                <thead>
                  <tr>
                    <th>Origin</th>
                    <th>Destination</th>
                    <th>Equipment</th>
                    <th>Rate</th>
                    <th>Source</th>
                  </tr>
                </thead>
                <tbody>
                  {scrapedData.map((load, index) => (
                    <tr key={index}>
                      <td>{load.origin}</td>
                      <td>{load.destination}</td>
                      <td>{load.equipmentType}</td>
                      <td>${Number(load.rate).toLocaleString()}</td>
                      <td>{load.source}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </Applayout>
  );
}

export default LoadboardScraper; 