import React, { useState } from 'react';
import { toast } from 'react-toastify';
import axiosClient from '../axios/axiosClient';

const LoadStatusUpdate = ({ loadId, currentStatus, onStatusUpdate }) => {
  const [loading, setLoading] = useState(false);

  const statusOptions = [
    'ACTIVE',
    'ASSIGNED',
    'COMPLETED',
    'CANCELLED'
  ];

  const getStatusColor = (status) => {
    switch (status) {
      case 'ACTIVE':
        return 'primary';
      case 'ASSIGNED':
        return 'warning';
      case 'COMPLETED':
        return 'success';
      case 'CANCELLED':
        return 'danger';
      default:
        return 'secondary';
    }
  };

  const handleStatusChange = async (newStatus) => {
    try {
      setLoading(true);
      const response = await axiosClient.patch(`/admin/loads/${loadId}/status`, {
        status: newStatus
      });

      if (response.data.status === 'success') {
        toast.success('Load status updated successfully');
        onStatusUpdate(newStatus);
      }
    } catch (error) {
      toast.error(error.response?.data?.message || 'Failed to update load status');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="dropdown">
      <button 
        className={`btn btn-${getStatusColor(currentStatus)} btn-sm dropdown-toggle`}
        type="button"
        data-bs-toggle="dropdown"
        disabled={loading}
      >
        {loading ? 'Updating...' : currentStatus}
      </button>
      <ul className="dropdown-menu">
        {statusOptions.map(status => (
          <li key={status}>
            <a 
              className={`dropdown-item ${currentStatus === status ? 'active' : ''}`}
              href="#"
              onClick={(e) => {
                e.preventDefault();
                handleStatusChange(status);
              }}
            >
              {status}
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default LoadStatusUpdate; 