import React, { useState, useEffect, useContext, useRef } from 'react';
import Applayout from '../layout/applayout';
import AuthContext from '../../context/AuthContext';
import { toast } from 'react-toastify';
import axiosClient from '../../axios/axiosClient';
import { Link } from 'react-router-dom';

const styles = {
  profileImageContainer: {
    position: 'relative',
    width: '150px',
    margin: '0 auto'
  },
  profileImage: {
    width: '150px',
    height: '150px',
    objectFit: 'cover',
    borderRadius: '50%'
  },
  editOverlay: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    background: 'rgba(0,0,0,0.5)',
    borderRadius: '50%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    opacity: 0,
    transition: 'all 0.3s ease',
    cursor: 'pointer',
    zIndex: 1
  },
  editIcon: {
    color: '#fff',
    width: '24px',
    height: '24px'
  }
};

function Profile() {
  const { user } = useContext(AuthContext);
  const [isEditing, setIsEditing] = useState(false);
  const [loading, setLoading] = useState(false);
  const [profileData, setProfileData] = useState({
    username: '',
    email: '',
    firstName: '',
    lastName: '',
    companyName: '',
    dotNumber: '',
    contactName: '',
    phone: '',
    website: '',
    loadBoardUrls: [],
    role: '',
    status: '',
    createdAt: '',
    updatedAt: ''
  });
  const fileInputRef = useRef(null);
  const [photoLoading, setPhotoLoading] = useState(false);

  useEffect(() => {
    fetchUserProfile();
  }, []);

  const fetchUserProfile = async () => {
    try {
      const response = await axiosClient.get('/auth/profile');
      if (response.data.status === 'success') {
        setProfileData(response.data.data);
      }
    } catch (error) {
      toast.error('Failed to fetch profile data');
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setProfileData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      const response = await axiosClient.put('/auth/profile/update', profileData);
      if (response.data.status === 'success') {
        toast.success('Profile updated successfully');
        setIsEditing(false);
      }
    } catch (error) {
      toast.error(error.response?.data?.message || 'Failed to update profile');
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const handlePhotoUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    try {
      setPhotoLoading(true);
      const formData = new FormData();
      formData.append('photo', file);

      const response = await axiosClient.post('/users/profile-photo', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        }
      });

      if (response.data.status === 'success') {
        await fetchUserProfile(); // Refresh profile data
        toast.success('Profile photo updated successfully');
      }
    } catch (error) {
      toast.error(error.message || 'Failed to update profile photo');
    } finally {
      setPhotoLoading(false);
    }
  };

  return (
    <Applayout>
      <div className="container-fluid">
        <div className="page-header">
          <div className="row">
            <div className="col">
              <div className="page-header-left">
                <h3>Profile</h3>
                <ol className="breadcrumb">
                  <li className="breadcrumb-item">
                    <i className="fa fa-home"></i>
                  </li>
                  <li className="breadcrumb-item active">Profile</li>
                </ol>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-xl-4 col-md-6 mb-4">
            <div className="card">
              <div className="card-body text-center">
                <div 
                  className="profile-image mb-3" 
                  style={styles.profileImageContainer}
                  onMouseEnter={(e) => {
                    const overlay = e.currentTarget.querySelector('[data-overlay]');
                    if (overlay) overlay.style.opacity = '1';
                  }}
                  onMouseLeave={(e) => {
                    const overlay = e.currentTarget.querySelector('[data-overlay]');
                    if (overlay) overlay.style.opacity = '0';
                  }}
                >
                  <input
                    type="file"
                    ref={fileInputRef}
                    onChange={handlePhotoUpload}
                    accept="image/*"
                    style={{ display: 'none' }}
                  />
                  <img
                    src={profileData.photo || "/assets/images/avtar/user.jpeg"}
                    alt="Profile"
                    style={styles.profileImage}
                  />
                  <div 
                    data-overlay
                    onClick={() => fileInputRef.current.click()}
                    style={{
                      ...styles.editOverlay,
                      opacity: photoLoading ? 0.7 : 0
                    }}
                  >
                    <i className="fa fa-edit" style={styles.editIcon}></i>
                  </div>
                </div>
                <h4>{`${profileData.firstName} ${profileData.lastName}`}</h4>
                <p className="text-muted">@{profileData.username}</p>
                <p className="badge bg-success">{profileData.role}</p>
                {!isEditing && (
                  <p>

                  <button
                    className="btn btn-primary"
                    onClick={() => setIsEditing(true)}
                  >
                    Edit Profile
                  </button>
                  </p>
                )}
              </div>
            </div>

            <div className="card mt-4">
              <div className="card-body">
                <h6 className="mb-3">Account Information</h6>
                <p><strong>Status:</strong> <span className="badge bg-success">{profileData.status}</span></p>
                <p><strong>Member Since:</strong> {formatDate(profileData.createdAt)}</p>
                <p><strong>Last Updated:</strong> {formatDate(profileData.updatedAt)}</p>
              </div>
            </div>
          </div>

          <div className="col-xl-8 col-md-6">
            <div className="card">
              <div className="card-body">
                <form onSubmit={handleSubmit}>
                  <h5 className="mb-4">Company Information</h5>
                  <div className="row g-3">
                    {/* Company fields */}
                    <div className="col-md-6">
                      <label className="form-label">Company Name</label>
                      <input
                        type="text"
                        className="form-control"
                        name="companyName"
                        value={profileData.companyName}
                        onChange={handleInputChange}
                        disabled={!isEditing}
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">DOT Number</label>
                      <input
                        type="text"
                        className="form-control"
                        name="dotNumber"
                        value={profileData.dotNumber}
                        onChange={handleInputChange}
                        disabled={!isEditing}
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">Contact Name</label>
                      <input
                        type="text"
                        className="form-control"
                        name="contactName"
                        value={profileData.contactName}
                        onChange={handleInputChange}
                        disabled={!isEditing}
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">Website</label>
                      <input
                        type="url"
                        className="form-control"
                        name="website"
                        value={profileData.website}
                        onChange={handleInputChange}
                        disabled={!isEditing}
                      />
                    </div>
                  </div>

                  <h5 className="mb-4 mt-4">Personal Information</h5>
                  <div className="row g-3">
                    {/* Personal fields */}
                    <div className="col-md-6">
                      <label className="form-label">Username</label>
                      <input
                        type="text"
                        className="form-control"
                        value={profileData.username}
                        disabled
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">Email</label>
                      <input
                        type="email"
                        className="form-control"
                        value={profileData.email}
                        disabled
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">First Name</label>
                      <input
                        type="text"
                        className="form-control"
                        name="firstName"
                        value={profileData.firstName}
                        onChange={handleInputChange}
                        disabled={!isEditing}
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">Last Name</label>
                      <input
                        type="text"
                        className="form-control"
                        name="lastName"
                        value={profileData.lastName}
                        onChange={handleInputChange}
                        disabled={!isEditing}
                      />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">Phone</label>
                      <input
                        type="tel"
                        className="form-control"
                        name="phone"
                        value={profileData.phone}
                        onChange={handleInputChange}
                        disabled={!isEditing}
                      />
                    </div>
                  </div>

                  {isEditing && (
                    <div className="mt-4">
                      <button
                        type="submit"
                        className="btn btn-primary me-2"
                        disabled={loading}
                      >
                        {loading ? 'Saving...' : 'Save Changes'}
                      </button>
                      <button
                        type="button"
                        className="btn btn-outline-secondary"
                        onClick={() => setIsEditing(false)}
                      >
                        Cancel
                      </button>
                    </div>
                  )}
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Applayout>
  );
}

export default Profile;
